import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// 引入修改meta标签依赖
import Meta from 'vue-meta'
Vue.use(Meta)

import './assets/css/global.css'
import './assets/css/style.css'
import './assets/css/wkeva.css'
import './assets/js/jquery.min'
import './assets/js/WkEpg3.2'
import './assets/js/Api'
import './assets/js/Util'
import './assets/js/Eva'


// 导入axios以及配置
import axios from 'axios'
axios.defaults.timeout = '3000ms'
axios.defaults.baseURL = 'http://112.18.251.228:10090/api'

axios.interceptors.request.use(config => {
  config.headers["Authorization"] = "Bearer AA==";
  return config
})

Vue.config.productionTip = false

// 挂载axios
Vue.prototype.$http = axios
Vue.prototype.pageobj = null

new Vue({
  router,
  //定义metaInfo
  metaInfo(){
    return {
      title: '四川梵宇EPG',
        meta:[
        {
          name:'page-view-size',
          content:'1920*720'
        }
      ]
    }
  },
  store,
  render: h => h(App)
}).$mount('#app')
