<template>
  <div>
    <div style="position: absolute;z-index: 3">
      {{$store.state.playURL}}
    </div>
    <div class="load" style="display:none;" id="dataLoad">
      <div class="load-out"></div>
      <div class="load-in"></div>
    </div>

    <!-- 播放器 -->
    <div class="player" id="player">
      <!--	<embed id="radioPlayer"  autostart="true" hidden="no" loop="true" width="1280" height="720" style="position:absolute;width:1280px; height:720px;top:0;left:0;" type="application/yst-player"></embed>-->
      <video
        :src="videoUrl"
        @timeupdate="updateTime"
        autoplay
        ref="myVideo"
        style="position:absolute;width:1280px; height:722px;top:0;left:0;" id="video"
        @keyup.enter="pauseOn"></video>
      <div class="playerControl" id="playerControl">
        <div class="scroll">
          <div id="scrollLine">
            <div class="scrollCircle">
            </div>
          </div>
        </div>
        <a href="javascript:;" id="pause"></a>
        <span id="time" :style="{visibility: (controlvisible?'visible':'hidden')}">{{nowTime}}/{{allTime}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  // import '../assets/js/videoControl'
  export default {
    data() {
      return {
        videoUrl: '',
        nowTime: '',
        forwardTime: '',
        videoTime: '',
        allTime: '',
        videoState: false,
        video: null,
        controlvisible: false,
        forwardFlag: false
      }
    },
    created() {
      this.init()
    },
    mounted() {
      var that = this
      // 处理回车暂停
      document.addEventListener('keydown', that.handleVideo)
      this.$refs.myVideo.addEventListener("play", () => {
        this.controlvisible = true
        this.videoState = true
        this.allTime = this.formatSeconds(this.$refs.myVideo.duration);
        this.forwardTime = parseInt(this.$refs.myVideo.duration / 60);
        this.forwardTime < 1 ? this.forwardTime = 1 : this.forwardTime;
        this.forwardTime > 15 ? this.forwardTime = 15 : this.forwardTime;
        // this.openTimer();
      });
      this.$refs.myVideo.addEventListener('waiting', () => {
        $('#dataLoad').css('display','block')
      })
      this.$refs.myVideo.addEventListener('playing', () => {
        $('#dataLoad').css('display','none')
      })
    },
    methods: {
      init: function () {
        var that = this
        // 处理视频源
        this.videoUrl = 'https://media.w3.org/2010/05/sintel/trailer.mp4'
        WkEpg.pageobj = null

        // 处理返回事件
        WkEpg.Nav.keyBackEvent = function () {
          document.removeEventListener('keydown', that.handleVideo)
          that.$refs.myVideo.onwaiting = null
          that.$refs.myVideo.onplay = null
          that.$refs.myVideo.onplaying = null
          that.$router.go(-1)
        }

      },
      handleVideo: function (e) {
        if (e.keyCode == 13) {
          if (this.$refs.myVideo.paused) {
            this.$refs.myVideo.play();
            this.videoState = true;
          } else {
            this.$refs.myVideo.pause();
            this.videoState = false;
          }
        } else if (e.keyCode == 39) {
          if (this.forwardFlag) return
          var that = this
          this.$refs.myVideo.currentTime += this.forwardTime;
          this.forwardFlag = true
          setTimeout(function () {
            that.forwardFlag = false
          },300)
        } else if (e.keyCode == 37) {
          if (this.forwardFlag) return
          var that = this
          this.$refs.myVideo.currentTime -= this.forwardTime;
          this.forwardFlag = true
          setTimeout(function () {
            that.forwardFlag = false
          },300)
        }
      },
      formatSeconds: function (value) {
        var theTime = parseInt(value);// 秒
        var theTime1 = 0;// 分
        var theTime2 = 0;// 小时
        // alert(theTime);
        if (theTime > 60) {
          theTime1 = parseInt(theTime / 60);
          theTime = parseInt(theTime % 60);
          // alert(theTime1+"-"+theTime);
          if (theTime1 > 60) {
            theTime2 = parseInt(theTime1 / 60);
            theTime1 = parseInt(theTime1 % 60);
          }
        }
        theTime < 10 ? theTime = '0' + theTime : theTime;
        theTime1 < 10 ? theTime1 = '0' + theTime1 : theTime1;
        theTime2 < 10 ? theTime2 = '0' + theTime2 : theTime2;

        var result = theTime;
        if (theTime1 > 0) {
          result = theTime1 + ':' + result;
        } else {
          result = '00:' + result;
        }
        if (theTime2 > 0) {
          result = theTime2 + ':' + result;
        }
        return result;
      },
      updateTime: function (e) {
        this.nowTime = this.formatSeconds(e.target.currentTime);
        this.videoTime = this.nowTime + '/' + this.allTime;
        $('#scrollLine').css("width", (e.target.currentTime * 1280 / e.target.duration) + 'px')
      }
    }
  }
</script>

<style scoped>
  /*load*/
  .load {
    width: 1280px;
    height: 720px;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 30;
  }

  .load-out {
    width: 128px;
    height: 128px;
    position: absolute;
    top: 296px;
    left: 576px;
    background: url(../assets/images/loading_out.png) center center no-repeat;
    -webkit-animation: loading 2s infinite linear;
  }

  @-webkit-keyframes loading {
    0% {
      -webkit-transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
    }
  }

  .load-in {
    width: 128px;
    height: 128px;
    position: absolute;
    top: 296px;
    left: 576px;
    background: url(../assets/images/loading_in.png) center center no-repeat;
  }

  .playerControl {
    position: absolute;
    transition: all .4s;
    bottom: 0;
    left: 0;
    width: 1280px;
    height: 90px;
    background-color: rgba(0, 0, 0, .5);
  }

  .playerControlMiss {
    transform: translateY(99px);
  }

  #pause {
    position: absolute;
    width: 60px;
    height: 60px;
    background-color: white;
    bottom: 0;
    transition: all .5s;
  }

  .pauseStop {
    border-radius: 60px;
  }

  .scroll {
    width: 1280px;
    height: 6px;
    background-color: black;
  }

  #scrollLine {
    position: absolute;
    left: 0;
    top: 0;
    width: 300px;
    height: 6px;
    background-color: white;
  }

  .scrollCircle {
    position: absolute;
    right: 0;
    transform: translate(50%, -40%);
    width: 20px;
    height: 20px;
    border-radius: 20px;
    background-color: white;
  }

  #time {
    position: absolute;
    bottom: 15px;
    left: 80px;
    font-size: 26px;
    color: white;
  }
</style>
