
(function (window, document, Util) {

    /**
     * @说明 提示消息
     *  Notice.echo()
     *  Notice.success()
     *  Notice.error()
     *  Notice.clear()
     **/
    var Notice = function (param) {
        param = param || {};
        param.top = param.top || 100;
        var
            /**
             * 容器
             **/
            _root =  Util.getSingle(function (){
                var a = Util.elemCreator({
                    tagName: "div",
                    props: { 'id': 'WkEva_notice_root','style': 'position:fixed; width:100%; text-align:center; top:'+param.top+'px; z-index:999; opacity:0.9;  font-size:18px;' },
                })
                return a.render();
            }),
            /**
             * 文本
             **/
            _txt = Util.elemCreator({
                tagName: "span",
                props: { 'style': 'padding: 0px 0px 0px 10px; font-size:18px; line-height:18px;' },
            }),
            /**
             * 标题
             **/
            _title = Util.elemCreator({
                tagName: "span",
                props: { 'style': 'background: #667eea;border-radius: 30px;padding: 0px 12px; font-size:18px;' },
                children: [ "Notice" ],
            }),
            /**
             * 背景
             **/
            _bg = Util.elemCreator({
                tagName: "span",
                props: { 'style': 'background:#434190; border-radius:30px; padding:10px 15px; color:#ffffff;' },
                children: [_title,_txt],
            }),
            /**
             * 组件单元
             **/
            _item = Util.elemCreator({
                tagName: "div",
                props: { 'style': 'width:100%;height:60px;' },
                children: [_bg],
            }),



            /**
             * 绑定容器
             **/
            _init = Util.getSingle(function () {
                document.body.appendChild( _root() )
                return _root()
            }),
            /**
             * 渲染
             **/
            _render = function (msg, interval, title) {

                var
                    n = null,
                    l = Util.getStrRealLen(msg),
                    a = _init(),
                    i = _item.render(),
                    e = i.getElementsByTagName("span")[1],
                    t = i.getElementsByTagName("span")[2];

                clearTimeout(n);
                interval = l > 40 ?  30 * 1000 : interval;//超长文本默认半分钟关闭
                t.innerHTML =  ( l > 40 ? "<marquee direction='left' scrolldelay='100' width=500 >" + msg + "</marquee>" : msg) || "";
                e.innerHTML = title || "Notice";
                a.appendChild(i);
                document.body.appendChild(a);
                interval === 0 ? true :  n = setTimeout(function () {
                    !!i && !!a.innerHTML ? a.removeChild(i):i=null;
                    clearTimeout(n),n = null,i = null;
                },interval||3000)

                return i
            },
            /**
             * 提示
             **/
            echo = function (msg, interval, title) {
                _render(msg, interval, title);
            },
            /**
             * 成功提示
             **/
            success = function (msg, interval, title) {
                var i = _render(msg, interval, title),
                    a = i.getElementsByTagName("span")[0],
                    e = i.getElementsByTagName("span")[1],
                    t = i.getElementsByTagName("span")[2];

                a.style.background = "#FFFFFF";
                e.style.background = "#57e93a";
                t.style.color = "#000000";
                e.innerHTML = title || "Ok";
            },
            /**
             * 错误提示
             **/
            error = function (msg, interval, title) {
                var i = _render(msg, interval, title),
                    a = i.getElementsByTagName("span")[0],
                    e = i.getElementsByTagName("span")[1],
                    t = i.getElementsByTagName("span")[2];

                a.style.background = "#FFFFFF";
                e.style.background = "#e9433a";
                t.style.color = "#000000";
                e.innerHTML = title || "Error";
            },
            /**
             * 清空
             **/
            clear = function () {
                var a = _root();
                a.innerHTML = "";
                return true;
            }


        this.echo = echo
        this.clear = clear;
        this.success = success;
        this.error = error;

    }

    /*
*
* @引入 wkepg.js Util.js
*
*   var mylog = new MyNotice()
    mylog.echo("50%","音量", false) 内容，title，是否永久显示
* */
    function MyToast(top) {
        this.boxtop = top || 600;
        this._type = "Notice";
        this._txt = null;
        this._tit = null;
        this._box = null;

        var
            my=this,
            _createHtml = Util.getSingle(function () {
                var txt = Util.elemCreator({
                        tagName: "span",
                        props: {
                            'style': 'padding: 0px 0px 0px 10px; font-size:18px; line-height:18px; '
                        },
                        children: ["Notice"],
                    }),
                    tit = Util.elemCreator({
                        tagName: "span",
                        props: {
                            'style': 'background: #667eea;border-radius: 30px;padding: 0px 12px; font-size:18px;'
                        },
                        children: ["Notice"],
                    }),
                    bg = Util.elemCreator({
                        tagName: "span",
                        props: {
                            'style': 'background:#434190; border-radius:30px; padding:10px 15px; color:#ffffff;'
                        },
                        children: [tit,txt],
                    }),
                    box = Util.elemCreator({
                        tagName: "div",
                        props: {
                            'style': 'position:fixed; width:100%; text-align:center; top:'+my.boxtop+'px; z-index:999; opacity:0.9; display:none; font-size:18px;'
                        },
                        children: [bg],
                    }),
                    spans = null;

                my._box = box.render();
                my._tit = my._box.getElementsByTagName("span")[1]
                my._txt = my._box.getElementsByTagName("span")[2]

                document.body.appendChild(my._box);

                return true;

            }),
            off = Util.debounce(function () {
                my._box.style.display = "none";
                my._txt.innerHTML = "";
            }, 3000),
            echo = function (msg, title, isoff) {
                _createHtml()
                my._box.style.display = "block";
                my._tit.innerHTML = title ? title:"Notice";
                my._txt.innerHTML = ( Util.getStrRealLen(msg) > 40 ? "<marquee direction='left' scrolldelay='100' width=500 >" + msg + "</marquee>":msg);
                if (!isoff) off();
            },
            clear = function () {
                 my._box.style.display = "none";
                 my._txt.innerHTML = "";
             }

        this.echo = echo
        this.off  = off
        this.clear = clear

    }



    /**
     * @说明 虚拟键盘
     *  Keyboard.onechoEvent()
     *  Keyboard.handleBack()
     *  Keyboard.handleClear()
     *  Keyboard.handleCapslock()
     **/
    var Keyboard = function (param) {
        this.onechoEvent = undefined;
        this.handleBack = undefined;
        this.capslock = param.capslock || false;
        this.area = param.area;
        this.pageobj = param.pageobj;

        /**
         * 定义
         **/
        var
            N = [
                { row:[ "1","2","3","4" ] },
                { row:[ "5","6","7","8" ] },
                { row:[ "9","0","back","clear" ] },
            ],
            A = [
                { row:[ "1","2","3","4","5","6","7","8","9","0","back","clear","capslock"] },
                { row:[ "a","b","c","d","e","f","g","h","i","j","k","l","m"] },
                { row:[ "n","o","p","q","r","s","t","u","v","w","x","y","z"] },
            ],
            M = {
                '@1':{ up:"1", low:"1", echo: mapEcho },
                '@2':{ up:"2", low:"2", echo: mapEcho },
                '@3':{ up:"3", low:"3", echo: mapEcho },
                '@4':{ up:"4", low:"4", echo: mapEcho },
                '@5':{ up:"5", low:"5", echo: mapEcho },
                '@6':{ up:"6", low:"6", echo: mapEcho },
                '@7':{ up:"7", low:"7", echo: mapEcho },
                '@8':{ up:"8", low:"8", echo: mapEcho },
                '@9':{ up:"9", low:"9", echo: mapEcho },
                '@0':{ up:"0", low:"0", echo: mapEcho },
                '@a':{ up:"A", low:"a", echo: mapEcho },
                '@b':{ up:"B", low:"b", echo: mapEcho },
                '@c':{ up:"C", low:"c", echo: mapEcho },
                '@d':{ up:"D", low:"d", echo: mapEcho },
                '@e':{ up:"E", low:"e", echo: mapEcho },
                '@f':{ up:"F", low:"f", echo: mapEcho },
                '@g':{ up:"G", low:"g", echo: mapEcho },
                '@h':{ up:"H", low:"h", echo: mapEcho },
                '@i':{ up:"I", low:"i", echo: mapEcho },
                '@j':{ up:"J", low:"j", echo: mapEcho },
                '@k':{ up:"K", low:"k", echo: mapEcho },
                '@l':{ up:"L", low:"l", echo: mapEcho },
                '@m':{ up:"M", low:"m", echo: mapEcho },
                '@n':{ up:"N", low:"n", echo: mapEcho },
                '@o':{ up:"O", low:"o", echo: mapEcho },
                '@p':{ up:"P", low:"p", echo: mapEcho },
                '@q':{ up:"Q", low:"q", echo: mapEcho },
                '@r':{ up:"R", low:"r", echo: mapEcho },
                '@s':{ up:"S", low:"s", echo: mapEcho },
                '@t':{ up:"T", low:"t", echo: mapEcho },
                '@u':{ up:"U", low:"u", echo: mapEcho },
                '@v':{ up:"V", low:"v", echo: mapEcho },
                '@w':{ up:"W", low:"w", echo: mapEcho },
                '@x':{ up:"X", low:"x", echo: mapEcho },
                '@y':{ up:"Y", low:"y", echo: mapEcho },
                '@z':{ up:"Z", low:"z", echo: mapEcho },
                '@capslock':{ up:"小写", low:"大写", echo: mapEcho },
                '@clear':{ up:"清空", low:"清空", echo: mapEcho },
                '@back':{ up:"回退", low:"回退", echo: mapEcho },
                '@':{ up:"", low:"", echo: mapEcho },
            };
        //私有
        var self = this,
            len = this.pageobj.areas.length,
            input = "",
            itemId = param.itemId,
            type = param.type,
            close = param.close||false,
            rootcss = param.rootcss||{left:0,top:0},
            layout = param.layout || ( type === "number" ? N: type === "all" ? A:A );



        /**
         * 创建dom
         **/
        var _createHtml = close || (function () {

            var child = []
            for (var r = 0; r < layout.length; r++) {
                var row = layout[r].row, children = [], i, spans;
                for (var c = 0; c < row.length; c++) {
                    i = row[c];
                    switch(i){
                        case "back": i = "回退";break;
                        case "clear": i = "清空";break;
                        case "capslock": i = M['@capslock'].echo();break;
                        default:
                            if (!!i && self.capslock) i = i.toUpperCase();
                            break;
                    }
                    spans = WkEpg.Util.elemCreator({
                        tagName: "p",
                        children:[i],
                        props:{ 'id':itemId+(r*row.length+c) }
                    })
                    children.push(spans)
                }
                var divs = WkEpg.Util.elemCreator({
                    tagName: "div",
                    props: { 'class':"WkEva_keyboard_tr" },
                    children:children
                })
                child.push(divs)
            }
            var root = WkEpg.Util.elemCreator({
                tagName: "div",
                props: { 'id':"WkEva_keyboard_root", 'style':'left:'+rootcss.left+'px; top:'+rootcss.top+'px;' },
                children:child
            })
            document.body.appendChild(root.render())
        })()

        /**
         * 绑定
         **/
        var _render = (function () {
            var newmap = [];
            for (var n = 0, l = layout.length; n < l; n++) {
                Array.prototype.push.apply(newmap, layout[n].row);
            }

            self.pageobj.areas[len] = self.area, self.pageobj.areas[len].id = len, self.pageobj.areas[len].pageobj = self.pageobj;
            self.area.areaOkEvent = function () {
                var k = "@"+(newmap[this.curindex].toLowerCase())
                switch ( k ) {
                    case "@back": self.handleBack();break;
                    case "@clear": self.handleClear();break;
                    case "@capslock": self.handleCapslock();break;
                    case "@": break;
                    default:
                        var s = M[k].echo();
                        input += s;
                        self.onechoEvent && self.onechoEvent( s, input )
                        break;
                }
            }
        })()

        /**
         * 回退
         **/
        this.handleBack = function () {
            input = String(input).slice(0, String(input).length - 1);
            self.onechoEvent && self.onechoEvent( "", input )
        }
        /**
         * 清空
         **/
        this.handleClear = function () {
            input = "";
            self.onechoEvent && self.onechoEvent( "", input )
        }
        /**
         * 大小写
         **/
        this.handleCapslock = function () {
            var t;
            self.capslock = !self.capslock;
            for (var i = 0, l = self.area.doms.length; i < l; i++) {
                t = Util.$(itemId+i).innerHTML.toLowerCase();
                switch( t ){
                    case "回退": t = "back";break;
                    case "清空": t = "clear";break;
                    case "大写": t = "capslock";break;
                    case "小写": t = "capslock";break;

                }
                Util.$(itemId+i).innerHTML = M['@'+t].echo();
            }
        }
        /**
         * 获得数据
         **/
        this.getInput = function () {
            return input
        }
        /**
         * 修改数据
         **/
        this.setInput = function (i) {
            return input = i
        }


        function mapEcho() {
            return self.capslock ? this.up:this.low;
        }

    }


    /**
     * 滚动条
     * @param parent 容器id名
     * @param bar 锚点id名
     * @param bg 容器背景id名
     * @method render(总页数，当前页数)
     * @return Object 实例对象
     */
    var Scroll = function (param) {

        param = param || {};
        this.scrollSpacing = 20;
        this.parent = param.parent || "WkEva_scroll_root";
        this.bar = param.bar || "WkEva_scroll_bar";
        this.bg = param.bg || "WkEva_scroll_barbg";
        this.scrollBoxHeight = undefined;
        this.pagecount = undefined;
        this.curpage = undefined;


        this.createHtml = function () {

            var barbg = window['WkEpg'].Util.elemCreator({
                tagName: "div",
                props: {
                    'id': this.bg,
                    'style': 'position:relative; margin:0 auto; width:24px; height:480px; background-color:#424557; opacity:0.9; border-radius:16px;'
                }
            })
            var bar = window['WkEpg'].Util.elemCreator({
                tagName: "div",
                props: {
                    'id': this.bar,
                    'style': "position:absolute; left:7px; width:10px; height:6px; background-color:#47c5ff; border-radius:6px; z-index:5;"
                }
            });
            var parent =  window['WkEpg'].Util.elemCreator({
                tagName: "div",
                children: [barbg, bar],
                props: {
                    'id': this.parent,
                    'style': "position:absolute; left:1200px; top:130px; width:24px; height:480px; text-align:center;"
                }
            })

            document.body.appendChild(parent.render());
        }
        this.render = function (curpage, pagecount) {
            var scrollsH, moveScroll, perScroll, top, scrollBar, scrollBox;
            scrollBar = Util.$( this.bar );
            scrollBox = Util.$( this.parent );
            this.pagecount =  pagecount;
            this.curpage = curpage;
            if(this.pagecount > 1){
                scrollsH = parseInt(this.scrollBoxHeight) / this.pagecount;
                moveScroll = parseInt(this.scrollBoxHeight) - scrollsH - this.scrollSpacing;
                perScroll = moveScroll / (this.pagecount - 1);
                top = (this.curpage - 1) * perScroll + (this.scrollSpacing/2);
                scrollBar.style.height = scrollsH + "px";
                scrollBar.style.top = top + "px";
                scrollBox.style.display = "block";
            }else{
                scrollBox.style.display = "none";
            }
        }

        this.createHtml();
        this.scrollBoxHeight = Util.$(this.parent).offsetHeight;
        Util.$(this.parent).style.display = "none";


    }


    /**
     * 弹窗控制器
     * @method show()
     * @method hide()
     */
    var AlertsController = function (param) {
        this.show = undefined
        this.hide = undefined
        this.state = false
        this.curareaid = -1
        this.record = [0,0];

        var pageobj = param.pageobj, self = this, _notrecord = false;
        pageobj.ok = Util.beforeFn( pageobj.ok || function () {}, function () {
            if (!self.state && !_notrecord){
                self.record[0] = pageobj.curareaid;
                self.record[1] = pageobj.areas[this.curareaid].curindex;
            }else {
                _notrecord = false;
            }
        } )

        this.show = function (areaid, indexid, notfocus, notrecord) {
            this.curareaid === -1 ? null: Util.$("WkEva_alerts_"+this.curareaid).style.display = "none";
            this.curareaid = areaid;
            this.state = true;
            _notrecord = notrecord || false;
            Util.$("WkEva_alerts_"+areaid).style.display = "block";
            !notfocus ? pageobj.changefocus(areaid, indexid): null;
        }
        this.hide = function (areaid, indexid) {
            this.state = false;
            Util.$("WkEva_alerts_"+this.curareaid).style.display = "none";
            !areaid && areaid !== 0  ? pageobj.changefocus(this.record[0], this.record[1]) : pageobj.changefocus(areaid, indexid||0);
        }
    }




    /**
     * 选集组建
     */
    var Episodes = function (Option) {
        this.dir = Option.dir || 0
        this.episodeTotal = Option.episodeTotal || 0

        this.episodeSize = Option.episodeSize
        this.episodeArea = Option.episodeArea

        this.paginationSize = Option.paginationSize
        this.paginationArea = Option.paginationArea

        this.onEpisodePageTurnChange = Option.onEpisodePageTurnChange || undefined
        this.pagination = new Pagination({
            dir:this.dir,
            paginationArea: this.paginationArea,
            onPageTurnChange: Option.onPaginationPageTurnChange,
            total:Math.ceil(this.episodeTotal/this.episodeSize),
            pageSize:this.paginationSize
        })

    }

    Episodes.prototype= {
        init: function () {
            this.pagination.init()

            var episodeSize = this.episodeSize,
                paginationSize = this.paginationSize,
                paginationArea = this.paginationArea,
                episodeArea = this.episodeArea,
                pagination = this.pagination,
                episodeTotal = this.episodeTotal,
                dir = this.dir,
                my = this
            ;

            episodeArea.changefocusAfterEvent = function () {
                var curindex = this.curindex + 1 + (this.curpage - 1) * episodeSize
                if (this.curindex == 0) {
                    var a = Math.ceil(curindex / episodeSize),
                        b = paginationArea.stayindexarray[dir] + (paginationArea.curpage - 1) * paginationSize + 1;
                    paginationArea.stayindexarray[dir] += a - b;
                    if (paginationArea.stayindexarray[dir] + 1 > paginationArea.datanum) {
                        paginationArea.pageturn(1)
                        paginationArea.stayindexarray[dir] = 0
                    }
                } else if (this.curindex + 1 == episodeSize) {
                    var a = Math.ceil(curindex / episodeSize),
                        b = paginationArea.stayindexarray[dir] + (paginationArea.curpage - 1) * paginationSize + 1;
                    paginationArea.stayindexarray[dir] += a - b;
                    if (paginationArea.stayindexarray[dir] + 1 == 0) {
                        paginationArea.pageturn(-1)
                        paginationArea.stayindexarray[dir] = paginationArea.datanum - 1
                    }
                }

            }

            pagination.onChange = function () {
                // var curindex = this.curindex + (this.curpage - 1) * paginationSize + 1;
                episodeArea.pageturn((this.curindex + (this.curpage - 1) * paginationSize + 1) - episodeArea.curpage)
            }

            episodeArea.areaPageTurnEvent = function (num) {
                if (this.pagecount <= 1) return;
                this.lock = true;
                var max = this.curpage * episodeSize;
                this.datanum = max < episodeTotal ? episodeSize : (max > episodeTotal ? episodeSize - (max - episodeTotal) : episodeSize)
                my.onEpisodePageTurnChange && my.onEpisodePageTurnChange.apply(this, [this.curindex+1, episodeSize])

            };

        }
    }


    var Pagination = function (Option) {
        this.current = Option.current || 1
        this.total = Option.total || 0
        this.pageSize = Option.pageSize || 3
        this.isLockFollow = Option.isLockFollow || false
        this.isVerticalPageTurn = Option.isVerticalPageTurn || false
        this.dir = Option.dir || 0

        this.paginationArea = Option.paginationArea
        this.onPageTurnChange = Option.onPageTurnChange
        this.onChange = undefined
    }
    Pagination.prototype = {
        init: function () {
            this.paginationArea.areaStayInitFocus = false;
            this.paginationArea.pagecount = Math.ceil(this.total / this.pageSize);
            this.paginationArea.curpage = Math.ceil(this.current / this.pageSize);
            this.paginationArea.stayindexarray[this.dir] = ((this.current - 1) % this.pageSize);
            this.isVerticalPageTurn ? this.paginationArea.setendwiseCrossturnpage(true) : this.paginationArea.setbroadwiseCrossturnpage(true); //设置翻页方向

            this.areaEvent( this.paginationArea )
        },
        areaEvent: function (curarea) {
            var my = this;
            curarea.areaOkEvent = function () {
                my.onChange && my.onChange.apply(this, [ this.curindex+1, my.pageSize ])
            }
            curarea.changefocusAfterEvent = function () {
                if ( my.isLockFollow ) return;
                my.onChange && my.onChange.apply(this, [ this.curindex+1, my.pageSize ])
            }
            curarea.areaPageTurnEvent = function (num) {
                if ( this.pagecount <= 1 ) return;
                this.lock = true;
                var max = this.curpage * my.pageSize;
                curarea.datanum = max < my.total ? my.pageSize : (max > my.total ? my.pageSize - (max - my.total) : my.pageSize)
                my.onPageTurnChange && my.onPageTurnChange.apply(this, [this.curindex+1, my.pageSize])
            };
        }
    }



    if(!window['WkEpg'].Eva) window['WkEpg'].Eva = {};
    window['WkEpg'].Eva.Notice = Notice
    window['WkEpg'].Eva.Toast = MyToast
    window['WkEpg'].Eva.Keyboard = Keyboard
    window['WkEpg'].Eva.Scroll = Scroll
    window['WkEpg'].Eva.AlertsController = AlertsController
    window['WkEpg'].Eva.Episodes = Episodes
    window['WkEpg'].Eva.Pagination = Pagination



})(window, document, WkEpg.Util)





