          area16 = WkEpg.AreaCreator(1, 3, [15, 14, 3, -1], "area16_div_", "className:item item_focus", "className:item");
          area17 = WkEpg.AreaCreator(2, 3, [5, -1, 18, -1], "area17_div_", "className:item item_focus", "className:item");
          area18 = WkEpg.AreaCreator(1, 2, [17, -1, 6, -1], "area18_div_", "className:item item_focus", "className:item");
          area19 = WkEpg.AreaCreator(1, 4, [-1, -1, 20, -1], "area19_div_", "className:item item_focus", "className:item");
          area20 = WkEpg.AreaCreator(1, 2, [19, -1, -1, -1], "area20_div_", "className:item item_focus", "className:item");
          area3.setbroadwiseCrossturnpage(true);
          area3.curpage = Math.floor(parseInt(that.c1Flag) / 5) + 1

          area1.staydirarray = [true, false, true, false];
          area3.staydirarray = [true, false, true, false];
          area5.staydirarray = [false, false, true, false];
