          area9.staydirarray = [true, false, true, false];
          area10.staydirarray = [true, false, true, false];
          area11.staydirarray = [true, false, true, false];
          area12.staydirarray = [true, false, true, false];
          area13.staydirarray = [true, false, true, false];
          area17.staydirarray = [true, false, true, false];
          area18.staydirarray = [true, false, false, false];
          area4.stablemoveindex = ["0-5>3", "0-5>3", "0-5>7", -1];
          area5.stablemoveindex = ["7-4>0", -1, -1, -1];
          area3.stablemoveindex = ["0-14>2,1-16>0,2-16>1,3-16>2,4-16>2", -1, -1, -1];
          area14.stablemoveindex = [-1, -1, -1, "2-16>0"];
          area15.stablemoveindex = [-1, -1, "1-16>2", -1];
          area16.stablemoveindex = ["2-15>1", "0-14>2", -1, -1];
          area17.stablemoveindex = [-1, -1, "5-18>1", -1];
          area18.stablemoveindex = ["1-17>5", -1, -1, -1];
          window.pageobj =  WkEpg.PageCreator(that.areaid, that.indexid, new Array(area0, area1, area2, area3, area4, area5, area6, area7, area8, area9, area10, area11, area12, area13, area14, area15, area16, area17, area18, area19, area20));
          // alertor = new WkEpg.Eva.AlertsController({pageobj:window.pageobj})
        },
        createOkEvent: function () {
          var bindTvlink = function () {
            if (!this.doms[this.curindex].tvlink) return;
            var link = this.doms[this.curindex].tvlink
            var msg = typeof link == "string"?link.substring(link.indexOf('=')+1):link.toString()
            var assetId = {
              assetId: msg
            }
            that.setReturnUrl('/detail',assetId)
            // window.location.href = this.doms[this.curindex].tvlink + and + "returnurl=" + encodeURIComponent(getReturnUrl(this.id, this.curindex));
          }

          area0.areaOkEvent = function () {
            switch (this.curindex) {
              case 0:
                that.setReturnUrl('/search')
                break;
              case 1:
                var colOrHis = {
                  colOrHis: 'col'
                }
                  that.setReturnUrl('/colAndHis',colOrHis)
                break;
              case 2:
                var colOrHis = {
                  colOrHis: 'his'
                }
                that.setReturnUrl('/colAndHis',colOrHis)
                break;
            }
          }
          area1.areaOkEvent = bindTvlink;
          area3.areaOkEvent = bindTvlink;
          area4.areaOkEvent = function () {
            that.pageLock[1] = false
            that.$store.commit('clickMagicCount')

            that.handlePage1()
          };
          area5.areaOkEvent = bindTvlink;
          area6.areaOkEvent = bindTvlink;
          area7.areaOkEvent = bindTvlink;
          area8.areaOkEvent = bindTvlink;
          area9.areaOkEvent = bindTvlink;
          area10.areaOkEvent = bindTvlink;
          area11.areaOkEvent = function () {
            that.pagenum = 0
            window.pageobj.changefocus(0,1)
          };
          area12.areaOkEvent = bindTvlink;
          area13.areaOkEvent = bindTvlink;
          area14.areaOkEvent = function () {
            var position = {}
            switch (this.curindex) {
              case 0:
                position = {colunm_index:'newfilm'}
                break;
              case 1:
                position = {colunm_index:'top'}
                break;
              case 2:
                position = {colunm_index:'dongm'}
                break;
            }
            that.setReturnUrl('/colunm',position)
          };
          area15.areaOkEvent = bindTvlink;
          area16.areaOkEvent = bindTvlink;
          area17.areaOkEvent = bindTvlink;
          area18.areaOkEvent = bindTvlink;
          area19.areaOkEvent = bindTvlink;
          area20.areaOkEvent = function () {
            switch (this.curindex) {
              case 0:
                $('#WkEva_alerts_20').hide()
                that.isExit = false
                window.pageobj.changefocus(0,1)
                break;
              case 1:
                console.log("quit app.")
                var str = 'finish';
                return str
                break;
            }
          }
        },
        createKeyFunction: function () {
          WkEpg.Nav.keyBackEvent = function () {
            if(that.pagenum === 0){
              if (that.isExit) {
                console.log("quit app.")
                var str = 'finish';
                return str
              }else {
                // notice.echo("再次点击「返回键」退出应用",null,"提示")
                that.getExitInfo()
                $('#WkEva_alerts_20').show()
                that.isExit = true
                window.pageobj.changefocus(20,0)
                // alertor.show(20,0)
                // that.handleExitFilm()
                // isExit = true
              }
            }else {
              that.pagenum = 0
              window.pageobj.changefocus(0,1)
            }
          }
        },
        changeMove: function () {
          window.pageobj.move = WkEpg.Util.afterFn(window.pageobj.move, function (dir) {
            // 屏蔽左右方向
            if (dir == 1 || dir == 3) return;
            switch (this.curareaid) {
              default:
                that.pagenum = 0;
                break;
              case 2:
              case 14:
              case 15:
              case 16:
                that.pagenum = 1;
                break;
              case 13:
              case 3:
                that.pagenum = 2;
                break;
              case 4:
              case 5:
                that.pagenum = 3;
                break;
              case 17:
                that.pagenum = 4;
                break;
              case 18:
                that.pagenum = 5;
                break;
              case 6:
                that.pagenum = 6;
                break;
              case 7:
                that.pagenum = 7;
                break;
              case 8:
                that.pagenum = 8;
                break;
              case 9:
                that.pagenum = 9;
                break;
              case 10:
                that.pagenum = 10;
                break;
              case 11:
                that.pagenum = 11;
                break;
            }
          })
        },

        //卡通大咖的换页逻辑
        createFunction: function () {
          area3.areaPageTurnEvent = function (num) {
            if (this.pagecount <= 1) return;
            this.lock = true;
            that.renderStar();
          };
        },

        // 内部为各区域切换焦点逻辑代码
        changeFunction: function () {
          // 设置魔法换片至女孩marqueeName
          var _focusAfterEventFnc = function (index) {
            return function () {
              if (this.doms[this.curindex] != undefined && WkEpg.Util.getStrRealLen(this.doms[this.curindex].contentName) > nameCut * 2) {
                $("#area" + index + "_txt_" + this.curindex).html("<marquee direction='left' scrolldelay='100'>" + this.doms[this.curindex].contentName + "</marquee>");
              }
            }
          }
          var _unfocusBeforeEventFnc = function (index) {
            return function () {
              if (this.doms[this.curindex] != undefined && WkEpg.Util.getStrRealLen(this.doms[this.curindex].contentName) > nameCut * 2) {
                $("#area" + index + "_txt_" + this.curindex).html(WkEpg.Util.getSubStr(this.doms[this.curindex].contentName, nameCut * 2, true));
              }
            }
          }
          for (var i = 5; i < 14; i++) {
            if (i !== 12) {
              window.pageobj.areas[i].changefocusAfterEvent = _focusAfterEventFnc.apply(this, [i]);
              window.pageobj.areas[i].changeunfocusBeforeEvent = _unfocusBeforeEventFnc.apply(this, [i]);
            }
          }
          window.pageobj.areas[19].changefocusAfterEvent = _focusAfterEventFnc.apply(this, [19]);
          window.pageobj.areas[19].changeunfocusBeforeEvent = _unfocusBeforeEventFnc.apply(this, [19]);


          _focusAfterEventFnc = null;
          _unfocusBeforeEventFnc = null;

          var _fn = function (wrapper, initCondition, moveleft) {
            var temparea = this;
            if (temparea.curindex < initCondition) {
              $("#" + wrapper).css("left", "0px");
            } else {
              var val = moveleft * (temparea.curindex - (initCondition - 1));
              $("#" + wrapper).css("left", "-" + val + "px");
            };
          }

          area1.changefocusAfterEvent = WkEpg.Util.beforeFn(area1.changefocusAfterEvent || function () {
          }, function () {
            _fn.apply(this, ["top_wrapper", 2, 226]);
            that.nowIndex = this.curindex
          })

          area6.changefocusAfterEvent = WkEpg.Util.beforeFn(area6.changefocusAfterEvent || function () {
          }, function () {
            _fn.apply(this, ["family_wrapper", 2, 450]);
          });

          area7.changefocusAfterEvent = WkEpg.Util.beforeFn(area7.changefocusAfterEvent || function () {
          }, function () {
            _fn.apply(this, ["global_wrapper", 4, 270]);
          });

          area8.changefocusAfterEvent = WkEpg.Util.beforeFn(area8.changefocusAfterEvent || function () {
          }, function () {
            _fn.apply(this, ["heji_wrapper", 2, 350]);
          });

          area9.changefocusAfterEvent = WkEpg.Util.beforeFn(area9.changefocusAfterEvent || function () {
          }, function () {
            _fn.apply(this, ["boy_wrapper", 2, 450]);
          });

          area10.changefocusAfterEvent = WkEpg.Util.beforeFn(area10.changefocusAfterEvent || function () {
          }, function () {
            _fn.apply(this, ["girl_wrapper", 2, 450]);
          });

        }
      }
      // FocusMove相关代码结束

      // // LoadData相关代码开始
      // function LoadData() {}
      // LoadData.prototype = {
      // }
      // // LoadData相关代码结束

      window.focus();
      if (!this.focusObj) this.focusObj = new FocusMove();
      window.pageobj.areas[3].pagecount = Math.ceil(this.c1Info.length / 5);
      this.renderStar();
      if (this.f2Info.length != 0) window.pageobj.areas[6].datanum = this.f2Info.length
      this.addTVLink(17,this.d1Info)
      this.addTVLink(12,that.promotions)
      this.addTVLink(6,this.f2Info)
      this.addTVLink(8,this.g1Info)
      this.addTVLink(18,this.e1Info)
      this.addTVLink(10,this.i2Info)
      this.addTVLink(1,this.mainInfo)
      this.addTVLink(15,this.b1Info)
      this.addTVLink(16,this.b3Info)
      this.addTVLink(3,this.c1Info)
      this.addTVLink(9,this.h2Info)
      // if (!dataObj) dataObj = new LoadData();
    },
    subName: function (val) {
      if (val.length>8) {
        return val.substring(0,6) + '...'
      } else{
        return val
      }
    },
    renderStar: function(){
      // vue判断渲染起点的flag
      var a3 = window.pageobj.areas[3]
      this.c1Flag = (a3.curpage-1) *5
      // 可上下左右移动的数量
      a3.datanum = this.c1Info.length-this.c1Flag>=5?'5':this.c1Info.length-this.c1Flag
      a3.lock = false;
      this.addTVLink(3,this.c1Info,this.c1Flag)
      // 左右箭头按钮渲染与否
      WkEpg.Util.$("area3_left").style.display = a3.curpage != 1 ? "block" : "none";
      WkEpg.Util.$("area3_right").style.display = a3.curpage < a3.pagecount ? "block" : "none";

    },
    setReturnUrl: function(hash,anyMsg) {
      var param = {
        returnUrl: window.location.hash,
        areaId: window.pageobj.curareaid,
        indexId: window.pageobj.areas[window.pageobj.curareaid].curindex,
        starpage: this.c1Flag,
        IndexPageNum: this.pagenum
      }
      if (anyMsg !== undefined) {
        for (var key in anyMsg) {
          param[key] = anyMsg[key]
        }
      }
      window.pageobj = null
      this.$store.commit('setReturnMsg', param)
      this.$router.push(hash)
    },
    handlePage0: async function () {
      if (this.pageLock[0]) return
      this.pageLock[0] = true
    },
    handlePage1: function () {
      if (this.pageLock[1]) return
      this.pageLock[1] = true
      this.$store.dispatch('getMagic')
    },
    handlePage2: function () {
      if (this.pageLock[2]) return
      this.pageLock[2] = true
      this.d1Info = this.$store.getters.getProByName('d1')
      this.e1Info = this.$store.getters.getProByName('e1')
      window.pageobj != null?this.addTVLink(17,this.d1Info):''
      window.pageobj != null?this.addTVLink(18,this.e1Info):''
    },
    handlePage3: function () {
      if (this.pageLock[3]) return
      this.pageLock[3] = true
      this.f2Info = this.$store.getters.getProByName('f2')
      window.pageobj != null?window.pageobj.areas[6].datanum = this.f2Info.length:''
      window.pageobj != null?this.addTVLink(6,this.f2Info):''
    },
    handlePage4: async function () {
      if (this.pageLock[4]) return
      this.pageLock[4] = true
      this.$store.dispatch('getFamousInfo')
    },
    handlePage5: function () {
      if (this.pageLock[5]) return
      this.pageLock[5] = true
      this.g1Info = this.$store.getters.getProByName('g1')
      window.pageobj != null?this.addTVLink(8,this.g1Info):''
    },
    handlePage6: function () {
      if (this.pageLock[6]) return
      this.pageLock[6] = true
      this.h2Info = this.$store.getters.getProByName('h2')
      window.pageobj != null?this.addTVLink(9,this.h2Info):''
    },
    handlePage7: function () {
      if (this.pageLock[7]) return
      this.pageLock[7] = true
      this.i2Info = this.$store.getters.getProByName('i2')
      window.pageobj != null?this.addTVLink(10,this.i2Info):''
    },
    random: function (lower, upper) {
      return Math.floor(Math.random() * (upper - lower)) + lower;
    },
    getExitInfo: function () {
      const page = this.random(0,5)
      this.$store.dispatch('getFamousInfo', page)
    }
  },
  watch: {
    pagenum: function (val) {
      WkEpg.Util.$("body_area").style.top = this.pagePosMap[val] + "px";
      WkEpg.Util.$("body_area").style.display = "block";
      switch (val) {
        case 0:
          this.handlePage0()
          break;
        case 1:
          this.handlePage1()
          break;
        case 2:
          this.handlePage1()
          this.handlePage2()
          break;
        case 3:
          this.handlePage1()
          this.handlePage2()
          this.handlePage3()
          break;
        case 4:
          this.handlePage1()
          this.handlePage2()
          this.handlePage3()
          this.handlePage4()
          break;
        case 5:
          this.handlePage1()
          this.handlePage2()
          this.handlePage3()
          this.handlePage4()
          this.handlePage5()
          break;//
        case 6:
          this.handlePage1()
          this.handlePage2()
          this.handlePage3()
          this.handlePage4()
          this.handlePage5()
          this.handlePage6()
          break;//
        case 7:
        case 8:
        case 9:
        case 10:
          this.handlePage1()
          this.handlePage2()
          this.handlePage3()
          this.handlePage4()
          this.handlePage5()
          this.handlePage6()
          this.handlePage7()
          break;
      };
      this.$nextTick(function(){
        /*DOM更新了*/
        if (!this.hasInit) {
          this.hasInit = true
          this.init()
          // if (window.pageobj.curareaid == window.pageobj.areas[6].id) window.pageobj.areas[6].changefocusAfterEvent();
          if (window.pageobj.curareaid == window.pageobj.areas[1].id) window.pageobj.areas[1].changefocusAfterEvent();
          if (window.pageobj.curareaid == window.pageobj.areas[6].id) window.pageobj.areas[6].changefocusAfterEvent();
          if (window.pageobj.curareaid == window.pageobj.areas[7].id) window.pageobj.areas[7].changefocusAfterEvent();
          if (window.pageobj.curareaid == window.pageobj.areas[8].id) window.pageobj.areas[8].changefocusAfterEvent();
          if (window.pageobj.curareaid == window.pageobj.areas[9].id) window.pageobj.areas[9].changefocusAfterEvent();
          if (window.pageobj.curareaid == window.pageobj.areas[10].id) window.pageobj.areas[10].changefocusAfterEvent();
        }
      })
    },
    magicInfo: function (val) {

      for (var i = 0;i < window.pageobj.areas[5].doms.length;i++) {
        window.pageobj.areas[5].doms[i].contentName = val[i].name
        window.pageobj.areas[5].doms[i].tvlink = val[i].id
      }
    },
    famousInfo: function (val) {
      for (var i = 0;i < window.pageobj.areas[7].doms.length;i++) {
        window.pageobj.areas[7].doms[i].contentName = val[i].name
        window.pageobj.areas[7].doms[i].tvlink = val[i].id
      }
    },
    exitInfo: function (val) {
      for (var i = 0;i < window.pageobj.areas[19].doms.length;i++) {
        window.pageobj.areas[19].doms[i].contentName = val[i].name
        window.pageobj.areas[19].doms[i].tvlink = val[i].id
      }
    }
  }
}
