<template>
  <div class="detail">
    <div id="body_area">
      <!--搜索框-->
      <div id="control_panel">
        <div id="control_input">
          <div id="placeholder">{{searchInfo.trim()?searchInfo.trim():'请输入节目首字母或全拼'}}</div>
        </div>
        <div id="keyboard">
          <table>
            <tr>
              <td id="area0_item_0">A</td>
              <td id="area0_item_1">B</td>
              <td id="area0_item_2">C</td>
              <td id="area0_item_3">D</td>
              <td id="area0_item_4">E</td>
              <td id="area0_item_5">1</td>
              <td id="area0_item_6">2</td>
              <td id="area0_item_7">3</td>
            </tr>
            <tr>
              <td id="area0_item_8">F</td>
              <td id="area0_item_9">G</td>
              <td id="area0_item_10">H</td>
              <td id="area0_item_11">I</td>
              <td id="area0_item_12">J</td>
              <td id="area0_item_13">4</td>
              <td id="area0_item_14">5</td>
              <td id="area0_item_15">6</td>
            </tr>
            <tr>
              <td id="area0_item_16">K</td>
              <td id="area0_item_17">L</td>
              <td id="area0_item_18">M</td>
              <td id="area0_item_19">N</td>
              <td id="area0_item_20">O</td>
              <td id="area0_item_21">7</td>
              <td id="area0_item_22">8</td>
              <td id="area0_item_23">9</td>
            </tr>
            <tr>
              <td id="area0_item_24">P</td>
              <td id="area0_item_25">Q</td>
              <td id="area0_item_26">R</td>
              <td id="area0_item_27">S</td>
              <td id="area0_item_28">T</td>
              <td id="area0_item_29">U</td>
              <td id="area0_item_30">V</td>
              <td id="area0_item_31">0</td>
            </tr>
            <tr>
              <td id="area1_item_0">W</td>
              <td id="area1_item_1">X</td>
              <td id="area1_item_2">Y</td>
              <td id="area1_item_3">Z</td>
              <td id="area2_item_0"></td>
              <td id="area2_item_1"></td>
            </tr>
          </table>
        </div>
      </div>
      <!--搜索结果-->
      <div id="result_panel">
        <!--搜索标题-->
        <div id="result_title">
          <div class="pic"><img src="../assets/images/search/search_icon.png" alt=""></div>
          <div id="result_remind" :style="{display:(total == -1?'block':'none')}">搜索结果</div>
          <div id="result_text" :style="{display:(total != -1?'block':'none')}">“<span id="special_str">{{searchInfo}}</span>”搜索结果共&nbsp;&nbsp;<span id="special_num">{{total}}</span>&nbsp;&nbsp;条</div>
        </div>
        <!--节目-->
        <div id="result_program" class="area area_3" :style="{display:(searchResult.length == 0?'none':'block')}">
          <div class="item" id="area3_item_0" :style="{display:(!searchResult[0]?'none':'block'),top: '40px'}">
            <div class="item_bg"></div>
            <div class="pic"><img id="area3_pic_0" :src="!searchResult[0]?'':baseURL+searchResult[0].posters.vertical"></div>
            <div class="txt" id="area3_txt_0">{{!searchResult[0]?'':subName(searchResult[0].name)}}</div>
            <div class="select"></div>
          </div>
          <div class="item" id="area3_item_1" :style="{display:(!searchResult[1]?'none':'block'),left: '145px',top: '40px'}">
            <div class="item_bg"></div>
            <div class="pic"><img id="area3_pic_1" :src="!searchResult[1]?'':baseURL+searchResult[1].posters.vertical" ></div>
            <div class="txt" id="area3_txt_1">{{!searchResult[1]?'':subName(searchResult[1].name)}}</div>
            <div class="select"></div>
          </div>
          <div class="item" id="area3_item_2" :style="{display:(!searchResult[2]?'none':'block'),left: '290px',top: '40px'}">
            <div class="item_bg"></div>
            <div class="pic"><img id="area3_pic_2" :src="!searchResult[2]?'':baseURL+searchResult[2].posters.vertical"></div>
            <div class="txt" id="area3_txt_2">{{!searchResult[2]?'':subName(searchResult[2].name)}}</div>
            <div class="select"></div>
          </div>
          <div class="item" id="area3_item_3" :style="{display:(!searchResult[3]?'none':'block'),top: '240px'}">
            <div class="item_bg"></div>
            <div class="pic"><img id="area3_pic_3" :src="!searchResult[3]?'':baseURL+searchResult[3].posters.vertical"></div>
            <div class="txt" id="area3_txt_3">{{!searchResult[3]?'':subName(searchResult[3].name)}}</div>
            <div class="select"></div>
          </div>
          <div class="item" id="area3_item_4" :style="{display:(!searchResult[4]?'none':'block'),left: '145px',top: '240px'}">
            <div class="item_bg"></div>
            <div class="pic"><img id="area3_pic_4" :src="!searchResult[4]?'':baseURL+searchResult[4].posters.vertical"></div>
            <div class="txt" id="area3_txt_4">{{!searchResult[4]?'':subName(searchResult[4].name)}}</div>
            <div class="select"></div>
          </div>
          <div class="item" id="area3_item_5" :style="{display:(!searchResult[5]?'none':'block'),left: '290px',top: '240px'}">
            <div class="item_bg"></div>
            <div class="pic"><img id="area3_pic_5" :src="!searchResult[5]?'':baseURL+searchResult[5].posters.vertical"></div>
            <div class="txt" id="area3_txt_5">{{!searchResult[5]?'':subName(searchResult[5].name)}}</div>
            <div class="select"></div>
          </div>
        </div>
        <!--滚动条-->
        <div class="item" id="scroll_box" :style="{display:(searchResult.length == 0?'none':'block'),left: '430px',top: '80px'}">
          <div class="item" id="scroll_bar"></div>
          <div class="scrolls" :style="{left: '7px',top: 342/Math.ceil(total / 6)*(searchPage-1)+10+'px'}">
            <div class="scroll scroll_0"></div>
            <div class="scroll scroll_1" :style="{height:342/Math.ceil(total / 6)+'px'}"></div>
            <div class="scroll scroll_2"></div>
          </div>
        </div>
        <!--箭头-->
        <div class="arrow_control" :style="{left: '203px',top: '464px',display: (searchPage == Math.ceil(total / 6)||searchResult.length==0?'none':'block')}"><img src="../assets/images/search/search_arrow_down.png" alt=""></div>
      </div>
    </div>
  </div>
</template>

<script>
  import { TOPDRAW_API_SERVER } from '../assets/js/config'
  export default {
    data () {
      return {
        searchInfo: '',
        focusObj: null,
        searchPage: 1,
        searchResult: [],
        total: -1,
        baseURL: ''
      }
    },
    methods: {
      addTVLink: function () {
        if (this.searchResult.length == 0) return
        var leng = window.pageobj.areas[3].datanum
        for (var i = 0;i < leng;i++) {
          window.pageobj.areas[3].doms[i].tvlink = this.searchResult[i].id
        }
      },
      subName: function (val) {
        if (val.length>5) {
          return val.substring(0,4) + '...'
        } else{
          return val
        }
      },
      init: function () {
        var area0, area1, area2, area3;
        var that = this
        this.baseURL = TOPDRAW_API_SERVER
        //焦点移动类
        function FocusMove() {
          this.createArea();
          this.createFunction();
          // this.marqueeName();
        }

        FocusMove.prototype = {
          createArea: function () {
            //@ Array(上、左、下、右)
            //@ area.stablemoveindex("fromIndex-toId>toIndex")
            area0 = WkEpg.AreaCreator(4, 8, [-1, -1, 1, -1], "area0_item_", "className:item_focus", "className:");
            area0.stablemoveindex = ["0-0>0", -1, "24-1>0,25-1>1,26-1>2,27-1>3,28-2>0,29-2>0,30-2>1,31-2>1", "7-3>0,15-3>0,23-3>0,31-3>3"];

            area1 = WkEpg.AreaCreator(1, 4, [0, -1, -1, 2], "area1_item_", "className:item_focus", "className:");
            area1.stablemoveindex = ["0-0>24,1-0>25,2-0>26,3-0>27", -1, -1, "3-2>0"];

            area2 = WkEpg.AreaCreator(1, 2, [0, 1, -1, -1], "area2_item_", "className:item_focus", "className:");
            area2.stablemoveindex = ["0-0>28,1-0>30", "0-1>3", -1, "0-3>3"];

            area3 = WkEpg.AreaCreator(2, 3, [-1, 0, -1, -1], "area3_item_", "className:item item_focus", "className:item");
            area3.stablemoveindex = [-1, "0-0>7,3-0>31", -1, -1];

            window.pageobj = WkEpg.PageCreator(0, 0, new Array(area0, area1, area2, area3));

            area3.setendwiseCrossturnpage(true);
            area3.curpage = 1;
          },
          createFunction: function () {
            area1.areaOkEvent = area0.areaOkEvent = function () {
              that.searchInfo += this.doms[this.curindex].htmldom.innerHTML
            }
            area2.areaOkEvent = function () {
              switch (this.curindex) {
                case 0:
                  that.searchInfo = ''
                  break;
                case 1:
                  if (that.searchInfo.length > 0) {
                    that.searchInfo = that.searchInfo.slice(0,that.searchInfo.length-1)
                  }
                  break;
              }
            }
            area3.areaOkEvent = function () {
              var param = {assetId:this.doms[this.curindex].tvlink,searchVal:that.searchInfo}
              that.setReturnUrl('/detail',param)
            }
            area3.areaPageTurnEvent = async function (num) {
              that.searchPage+=num
              const { data: res } = await that.$http.get('/search', {
                params: {
                  keyword: that.searchInfo,
                  page: that.searchPage,
                  pageSize: 6
                }
              })
              that.searchResult = res.data
              window.pageobj.areas[3].datanum = that.searchResult.length
              that.addTVLink()
          }
            WkEpg.Nav.keyBackEvent = function() {
              if(that.searchInfo != ''){
                that.searchInfo = that.searchInfo.slice(0,that.searchInfo.length-1)
                if (that.searchInfo === '') {
                  window.pageobj.changefocus(0,0)
                }
                that.$store.commit('setReturnMsg', {searchVal:that.searchInfo})
              } else {
                that.$router.go(-1)
              }
            }
        }
        }

        window.focus();
        if (!this.focusObj) this.focusObj = new FocusMove();
        this.$store.state.searchVal!==''?this.searchInfo=this.$store.state.searchVal:''
        // if (!this.domControlObj) this.domControlObj = new DomControl();
        // if (!this.dataObj) this.dataObj = new loadData();
      },
      setReturnUrl: function(hash,anyMsg) {
        var param = {
          returnUrl: window.location.hash
        }
        if (anyMsg !== undefined) {
          for (var key in anyMsg) {
            param[key] = anyMsg[key]
          }
        }
        window.pageobj = null
        this.$store.commit('setReturnMsg', param)
        this.$router.push(hash)
      }
    },
    mounted () {
      this.init()
    },
    watch: {
      searchInfo: async function (val) {
        // 检测变化初始化
        this.searchPage = window.pageobj.areas[3].curpage= 1
        this.total = -1
        this.searchResult = []
        window.pageobj.areas[0].directions[3] = -1;
        window.pageobj.areas[2].directions[3] = -1;

        // 检测结果不为初始值''时
        if (val != '') {
          const { data: res } = await this.$http.get('/search', {
            params: {
              keyword: this.searchInfo,
              page: this.searchPage,
              pageSize: 6
            }
          })
          this.searchResult = res.data
          window.pageobj.areas[3].datanum = this.searchResult.length
          this.addTVLink()
          this.total = res.meta.pagination.total
          window.pageobj.areas[3].pagecount = Math.ceil(this.total / 6)
        }
      },
      searchResult: function (val) {
        if (val.length == 0) return
        window.pageobj.areas[0].directions[3] = 3;
        window.pageobj.areas[2].directions[3] = 3;
      }
    }
  }
</script>

<style scoped>
  *{-webkit-box-sizing: border-box;-moz-box-sizing: border-box;box-sizing: border-box;}
  body{width: 1280px;height: 720px;background: #1B2A3D}
  #body_area{position: absolute;top: 0;left: 0;height: 720px;width: 1280px;overflow: hidden;}
  img{vertical-align: top;}

  /*绝对定位*/
  .item,
  .pic,
  .txt,
  .select,
  .arrow_control,
  .item_bg,
  .area,
  #body_area {position: absolute;}

  #control_panel{position: absolute;left: 87px;top: 145px;}
  #control_input{width:565px;height:51px;background: url("../assets/images/search/search_bg.png");}
  #placeholder {font-size: 28px;padding-left: 60px;line-height: 50px;height: 50px;}
  #client_input {font-size: 28px;padding-left: 60px;line-height: 50px;height: 50px;display: none;}

  #keyboard{margin-top:19px;}
  table{}
  table td{display: inline-block;margin: 6px;width:57px;height:57px;text-align: center;line-height:57px;background: url("../assets/images/search/letter_bg.png") no-repeat;font-size:36px;font-weight:bold;-webkit-text-stroke: 2px #000;}
  table td.item_focus{background: url("../assets/images/search/letter_bg_s.png");}
  #area0_item_5, #area0_item_6, #area0_item_7, #area0_item_13, #area0_item_14,
  #area0_item_15, #area0_item_21, #area0_item_22, #area0_item_23, #area0_item_31{background: url("../assets/images/search/number_bg.png") no-repeat;}
  #area0_item_5.item_focus,
  #area0_item_6.item_focus,
  #area0_item_7.item_focus,
  #area0_item_13.item_focus,
  #area0_item_14.item_focus,
  #area0_item_15.item_focus,
  #area0_item_21.item_focus,
  #area0_item_22.item_focus,
  #area0_item_23.item_focus,
  #area0_item_31.item_focus{background: url("../assets/images/search/number_bg_s.png") no-repeat;}
  #area2_item_0{background: url("../assets/images/search/clear.png");width: 129px;height: 58px;}
  #area2_item_1{background: url("../assets/images/search/back.png");width: 129px;height: 58px;}
  #area2_item_0.item_focus{background-image: url("../assets/images/search/clear_s.png");}
  #area2_item_1.item_focus{background-image: url("../assets/images/search/back_s.png");}

  #result_panel{position: absolute;left:737px;top:121px;}
  #result_remind{padding-left: 60px;height:40px;line-height:40px;font-size:32px;}
  #result_text{padding-left: 60px;height:40px;line-height:40px;font-size:32px;display: none;}
  #special_str, #special_num{font-size:36px;color:#FFF000;}
  .txt{width:112px;height:28px;text-align: center;line-height:28px;top:154px;font-size: 20px;}
  #result_program .item .pic{left:4px;top:4px;}
  #result_program .item .pic img{width:107px;height:148px;border-radius: 7px;}
  .area_3 .item .select{width:112px;height:154px;border:5px solid #fff;border-radius: 10px;}
  .area_3 .item .item_bg{width: 112px;height:154px;border-radius: 10px;background-color: #493FC8;}

  #scroll_bar{width:23px;height:377px;background-color: #000;opacity:0.3;border-radius: 16px;}
  .scrolls{position: absolute;}
  .scroll{position: relative;line-height:0;left:0;top:0;width:10px;height:6px;background-color: #00A0E9;}
  .scroll_0{border-radius: 6px 6px 0 0;}
  .scroll_2{border-radius: 0 0 6px 6px;}
</style>
