<template>
  <div class="detail">
    <div id="body_area">
      <div style="position: absolute;top: 160px;left: 540px;font-weight: 300">全部节目 共<span id="total_num"
                                                                                          style="padding: 0 10px;color: #47c5ff;font-weight: 800">{{viewTotal}}</span>部
      </div>
      <!--      <div style="position: absolute;top: 586px;left: 0px;z-index: 20"><img src="../assets/images/dark_bg.png" alt=""></div>-->
      <div style="position: absolute;top: 30px;left: 560px;z-index: 20"><img id="title_img" src="" alt=""></div>
      <div :style="{visibility: (totalpage === curpage?'hidden':'visible' )}" id="arrows">
        <img src="../assets/images/down_arr.png" alt="">
        <p>下拉查看更多节目</p>
      </div>

      <div class="area area_2" style="left:50px;top: 140px;">
        <div class="item" id="area2_item_0">
          <div class="select"></div>
          <div class="active" :style="{display:(activeFlag === 0?'block':'none')}"></div>
          <div class="display"
               :style="{background: ('url('+icon[0]+') no-repeat'),padding: '0 0 0 35px',width: '30px',height: '31px',lineHeight: '31px',fontSize: '20px',zIndex: '20'}">
            <span>收藏记录</span></div>
        </div>
        <div class="item" id="area2_item_1" style="top: 50px;">
          <div class="select"></div>
          <div class="active" :style="{display:(activeFlag === 1?'block':'none')}"></div>
          <div class="display"
               :style="{background: ('url('+icon[1]+') no-repeat'),padding: '0 0 0 38px',width: '33px',height: '32px',lineHeight: '32px',fontSize: '20px',zIndex: '20'}">
            <span>历史记录</span></div>
        </div>
      </div>


      <div class="area area_0" style="left: 210px;top: 220px;">
        <!--第一行-->
        <div class="item" id="area0_item_0" :style="{left: '0px',visibility:(!viewInfo[0]?'hidden':'visible')}">
          <div class="select"></div>
          <div class="pic"><img id="area0_pic_0" :src="!viewInfo[0]?'':baseURL+viewInfo[0].program_posters.vertical">
          </div>
          <div class="txt" id="area0_txt_0">{{!viewInfo[0]?'':subName(viewInfo[0].program_name)}}</div>
        </div>
        <div class="item" id="area0_item_1" :style="{left: '230px',visibility:(!viewInfo[1]?'hidden':'visible')}">
          <div class="select"></div>
          <div class="pic"><img id="area0_pic_1" :src="!viewInfo[1]?'':baseURL+viewInfo[1].program_posters.vertical">
          </div>
          <div class="txt" id="area0_txt_1">{{!viewInfo[1]?'':subName(viewInfo[1].program_name)}}</div>
        </div>
        <div class="item" id="area0_item_2" :style="{left: '460px',visibility:(!viewInfo[2]?'hidden':'visible')}">
          <div class="select"></div>
          <div class="pic"><img id="area0_pic_2" :src="!viewInfo[2]?'':baseURL+viewInfo[2].program_posters.vertical">
          </div>
          <div class="txt" id="area0_txt_2">{{!viewInfo[2]?'':subName(viewInfo[2].program_name)}}</div>
        </div>
        <div class="item" id="area0_item_3" :style="{left: '690px',visibility:(!viewInfo[3]?'hidden':'visible')}">
          <div class="select"></div>
          <div class="pic"><img id="area0_pic_3" :src="!viewInfo[3]?'':baseURL+viewInfo[3].program_posters.vertical">
          </div>
          <div class="txt" id="area0_txt_3">{{!viewInfo[3]?'':subName(viewInfo[3].program_name)}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {TOPDRAW_API_SERVER} from '../assets/js/config'

  export default {
    data() {
      return {
        icon: [
          require('../assets/images/my/icon_lishi.png'),
          require('../assets/images/my/icon_shoucang.png')
        ],
        indexId: !this.$store.state.colOrHisIndex ? 0 : this.$store.state.colOrHisIndex,
        collectInfo: [],
        historyInfo: [],
        renderFlag: false,
        viewInfo: [],
        viewTotal: 0,
        activeFlag: -1,
        baseURL: TOPDRAW_API_SERVER,
        focusObj: null,
        colTotal: 0,
        hisTotal: 0,
        curpage: !this.$store.state.colOrHisPage ? 1 : this.$store.state.colOrHisPage,
        totalpage: 1,
        renderFlag: false
      }
    },
    created() {
      this.getBaseInfo()
    },
    mounted() {
      this.init()
    },
    methods: {
      getBaseInfo: async function () {
        switch (this.$store.state.colOrHis) {
          case "col":
            const {data: inf} = await this.$http.get('/user/collects', {
              params: {
                pageSize: 4,
                page: this.curpage
              }
            })
            this.collectInfo = inf.data
            this.colTotal = inf.meta.pagination.total
            this.activeFlag = 0
            console.log(this.collectInfo)
            break;
          case "his":
            const {data: res} = await this.$http.get('/user/records', {
              params: {
                pageSize: 4,
                page: this.curpage
              }
            })
            this.historyInfo = res.data
            this.hisTotal = res.meta.pagination.total
            this.activeFlag = 1
            console.log(this.historyInfo)
            break;
        }
      },
      init: function () {
        var that = this
        var area0, area2

        //焦点移动类
        function FocusMove() {
          this.createArea();
          this.createFunction();
        }

        FocusMove.prototype = {
          createArea: function () {
            area0 = WkEpg.AreaCreator(1, 4, [-1, 1, -1, -1], "area0_item_", "className:item item_focus", "className:item");
            area2 = WkEpg.AreaCreator(2, 1, [-1, -1, -1, 0], "area2_item_", "className:item item_focus", "className:item");

            window.pageobj = WkEpg.PageCreator(0, that.indexId, new Array(area0, area2));
            area2.staydirarray = [false, false, false, true];

            // 处理第一次的暗焦点
            var firstFlag = 0
            if (that.$store.state.colOrHis == 'his') firstFlag = 1
            area2.stayindexarray = [0, 0, 0, firstFlag];

          },
          createFunction: function () {

            area2.areaOkEvent = function () {
              // window.location.href = "detail.html?assetId=" + slicePrograms[this.curindex]['program_id'] + "&returnurl=" + encodeURIComponent(dataObj.getReturnUrl(URL, 1, this.curindex));
            }
            area0.areaOkEvent = function () {
              var assetId = {assetId: this.doms[this.curindex].tvlink}
              that.setReturnUrl('/detail', assetId)
            }

            WkEpg.Nav.keyBackEvent = function () {
              var param = {
                colOrHisPage: '',
                colOrHisIndex: ''
              }
              that.$store.commit('setReturnMsg', param)
              that.$router.go(-1)
            };

            // 处理上下转换节目
            area0.upFlag = true
            area0.downFlag = true
            area0.downEVENT = function () {
              if (that.curpage == that.totalpage) return
              that.curpage += 1
            }
            area0.upEVENT = function () {
              if (that.curpage == 1) return
              that.curpage -= 1
            }
            area0.changefocusAfterEvent = function () {
              that.renderFlag = true
              if (this.doms[this.curindex] != undefined) {
                $("#area0_txt_" + this.curindex).html("<marquee direction='left' scrolldelay='100'>" + (!that.viewInfo[this.curindex] ? '' : that.viewInfo[this.curindex].program_name) + "</marquee>");
              }
            }
            area0.changefocusBeforeEvent = function () {
              if (this.doms[this.curindex] != undefined) {
                $("#area0_txt_" + this.curindex).html(!that.viewInfo[this.curindex] ? '' : that.subName(that.viewInfo[this.curindex].program_name));
              }
            }
            area0.areaOutBeforeEvent = function () {
              if (this.doms[this.curindex] != undefined) {
                $("#area0_txt_" + this.curindex).html(!that.viewInfo[this.curindex] ? '' : that.subName(that.viewInfo[this.curindex].program_name));
              }
            }
            // 区域内切换焦点触发事件
            area2.changefocusAfterEvent = async function () {
              switch (this.curindex) {
                case 0:
                  if (that.collectInfo.length === 0 || !that.renderFlag) {
                    const {data: inf} = await that.$http.get('/user/collects', {
                      params: {
                        pageSize: 4,
                        page: 1
                      }
                    })
                    that.colTotal = inf.meta.pagination.total
                    that.collectInfo = inf.data
                  }
                  that.activeFlag = 0
                  break;
                case 1:
                  if (that.historyInfo.length === 0 || !that.renderFlag) {
                    const {data: res} = await that.$http.get('/user/records', {
                      params: {
                        pageSize: 4,
                        page: 1
                      }
                    })

                    that.hisTotal = res.meta.pagination.total
                    that.historyInfo = res.data
                  }
                  that.renderFlag = false
                  that.curpage = 1
                  that.activeFlag = 1
                  break;
              }
            };
          }
        }

        if (!this.focusObj) this.focusObj = new FocusMove();

      },
      subName: function (val) {
        if (val.length > 6) {
          return val.substring(0, 5) + '...'
        } else {
          return val
        }
      },
      addTVLink: function (areaId, infoName, num) {
        if (infoName.length == 0) return
        var leng = !window.pageobj.areas[areaId].datanum ? window.pageobj.areas[areaId].doms.length : window.pageobj.areas[areaId].datanum
        var step = num ? parseInt(num) : 0
        for (var i = 0; i < leng; i++) {
          window.pageobj.areas[areaId].doms[i].tvlink = infoName[i + step].program_id
        }
      },
      parseColHis: function () {
        if (this.activeFlag === 0) {
          return 'col'
        } else if (this.activeFlag === 1) {
          return 'his'
        }
      },
      setReturnUrl: function (hash, anyMsg) {
        var param = {
          colOrHis: this.parseColHis(),
          colOrHisPage: this.curpage,
          colOrHisIndex: window.pageobj.areas[0].curindex
        }
        if (anyMsg !== undefined) {
          for (var key in anyMsg) {
            param[key] = anyMsg[key]
          }
        }
        window.pageobj = null
        this.$store.commit('setReturnMsg', param)
        this.$router.push(hash)
      }
    },
    watch: {
      activeFlag: function (val) {
        if (val === 0) {
          this.viewInfo = this.collectInfo
          this.viewTotal = this.colTotal
        } else if (val == 1) {
          this.viewInfo = this.historyInfo
          this.viewTotal = this.hisTotal
        }
        $('.area_0 .item').css('display', 'block')
        window.pageobj !== null ? window.pageobj.areas[0].datanum = this.viewInfo.length : ''
      },
      viewInfo: function (val) {
        this.addTVLink(0, val)
        if (window.pageobj.curareaid !== 0) return
        window.pageobj.areas[0].changefocusAfterEvent()

        for (var i = 0;i<window.pageobj.areas[0].length;i++) {
          $("#area0_txt_" + i).html(!this.viewInfo[i] ? '' : this.subName(this.viewInfo[i].program_name));
        }

      },
      viewTotal: function (val) {
        this.totalpage = Math.ceil(val / 4)
      },
      curpage: async function (val) {
        if (!this.renderFlag) return
        switch (window.pageobj.areas[1].curindex) {
          case 0:
            const {data: inf} = await this.$http.get('/user/collects', {
              params: {
                pageSize: 4,
                page: val
              }
            })
            this.viewInfo = inf.data
            break;
          case 1:
            const {data: res} = await this.$http.get('/user/records', {
              params: {
                pageSize: 4,
                page: val
              }
            })
            this.viewInfo = res.data
            break;
        }
        // 渲染最后一页时数据不够修改datanum 以及 动态更改焦点
        window.pageobj.areas[0].datanum = this.viewInfo.length
        if (window.pageobj.areas[0].curindex >= window.pageobj.areas[0].datanum) window.pageobj.changefocus(0, window.pageobj.areas[0].datanum - 1)
        console.log(this.totalpage)
        console.log(this.curpage)
      }
    }
  }
</script>

<style scoped>
  body {
    width: 1280px;
    height: 720px;
    overflow: hidden;;
  }

  #arrows {
    position: absolute;
    top: 586px;
    left: 570px;
    z-index: 20;
    font-size: 16px;
    text-align: center
  }

  #body_area {
    position: absolute;
    width: 1280px;
    height: 720px;
    background: url("../assets/images/my/bg.jpg") no-repeat
  }

  .area,
  .item,
    /*.pic,*/
  .txt,
  .select,
  .display,
  .active,
  .item_bg {
    position: absolute;
    box-sizing: border-box
  }

  .area_0 .item {
    width: 172px;
    height: 224px;
    text-align: center;
    display: none
  }

  .area_0 .item .pic {
    text-align: center;
    width: 172px;
    height: 224px;
    line-height: 224px;
    overflow: hidden;
    border-radius: 10px;
    border: #00A0E9 solid 2px;
  }

  .area_0 .item .pic img {
    width: 172px;
    vertical-align: middle;
    border-radius: 10px;
  }

  .area_0 .item .txt {
    font-size: 28px;
    top: 250px;
    height: 30px;
    line-height: 30px;
    width: inherit
  }

  .area_0 .item .select {
    width: 176px;
    height: 228px;
    -webkit-border-radius: 10px;
    border-radius: 10px;
    border: 5px solid #fff;
    z-index: 10;
  }

  .area_3 span,
  .area_2 span {
    white-space: nowrap;
  }

  .area_3 .item .select,
  .area_2 .item .select {
    background: url("../assets/images/my/area2_item_s.png") no-repeat;
    width: 205px;
    height: 49px;
    left: -50px;
    top: -8px;
  }

  .active {
    background: url("../assets/images/my/icon_left.png") no-repeat;
    width: 14px;
    height: 16px;
    left: -24px;
    top: 6px;
    z-index: 50
  }

  .area_3 .item_focus span {
    color: #FFFFFF
  }


</style>
