/**
 * @Api
 */
import {TOPDRAW_API_SERVER} from "./config";
(function (window) {
    if (!(typeof(window["WkEpg"]) === "object" && window["WkEpg"] !== undefined)) window["WkEpg"] = {};

    /**
     * 定义
     */
    var Timeouter = null,//异步变量

        U = window["WkEpg"].Util

    ; //工具

    /**
     * 基于Promise封装$.ajax请求 默认GET请求
     */
    var ajaxPromise = function (option, queryid) {
      console.log(option)
      console.log(queryid)
        option = option || {};
        return new window["WkEpg"].Promise(function (resolve, reject) {
            $.ajax(U.assign({
                timestamp: new Date().getTime(),
                type: "GET",
                dataType: "json",
                success: function (data, status, rep) {
                  console.log(data)
                    if (option.dataType === "xml"){
                        // resolve(rep.responseXML)
                        resolve(rep)
                    } else {
                        resolve(data)
                    }
                },
                error: function (err) {
                  console.log(1)
                    var status = err.status || "";
                    if (!!window["WkEpg"].Eva) {
                        var notice = new window["WkEpg"].Eva.Notice();
                        notice.error("【"+queryid+"】 "+"请求失败. "+status+".")
                    }
                    reject(err)
                }
            },option));
        })
    }

    /**
     * 设置请求头
     */

    var cmsHeader =function(xhr){ //v1接口
        xhr.setRequestHeader('Accept', 'application/vnd.puxin.v1+json');
        xhr.setRequestHeader('Authorization', "Bearer " + encode64(MyApi.getUsername()));
    };

    var MyApi = {
        __authRes:{},
        promotionsData: undefined,

        /**
         * @aandroid
         */

        //用户id
        getUsername:function(){
            return CLIENT_ID
        },
        //返回平台 YNYDHW YNYDZX
        getPlatform:function(){

        },
        //token
        getUserToken:function(){

        },

        //退出app
        exit: function(){

        },
        /**
         * @auth 播放鉴权
         * @param ContentID 节目code
         * @param categorieId 栏目id
         */
        auth: function(ContentID, categorieId, data){
            var AUTH_ERROR = ERROR_DICTIONARY['auth'];
            var plat = "", fn, t = null;
            switch (plat) {
                case "PXWH":
                    fn = this.authQuery(ContentID, categorieId, data)
                    break;
                case "TEST":
                    fn = new window["WkEpg"].Promise(function (resolve, reject) {
                        t = setTimeout(function () {
                            resolve(1)
                            clearTimeout(t)
                        })
                    })
                    break;
                default:
                    fn = new window["WkEpg"].Promise(function (resolve, reject) {
                        t = setTimeout(function () {
                            reject(AUTH_ERROR)
                            clearTimeout(t)
                        })
                    })
                    break;
            }
            return fn
        },
        /**
         * @auth 播放鉴权 中兴
         * @param ContentID 节目code
         * @param tid 栏目id
         */
        authQuery:function(ContentID){


        },

        /**
         * @orderPay 业务订购
         * @param
         */
        orderPay: function (programID, categorieId, data) {
            return new window["WkEpg"].Promise(function (resolve, reject) {
                setTimeout(function () {
                    resolve(1)
                })
            })
        },

        /**
         * @categoriesPrograms 获取栏目下的节目
         * @param categorieId 栏目id
         *        data 请求参数
         */
        categoriesPrograms: function (categorieId, data) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/categories/"+categorieId+"/programs",
                data:data,
                beforeSend: cmsHeader
            }, 1001)

        },

        /**
         * @searchStar 搜索节目
         * @param programID 节目id
         */
        searchStar: function(data) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/zh_cn/search",
                data: data,
                beforeSend: cmsHeader
            }, 1002)
        },

        /**
         * @detailProgram 节目详情
         * @param programID 节目id 媒资id
         *        data 请求参数
         */
        detailProgram: function (programID,data) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/programs/" + programID,
                data:data,
                beforeSend: cmsHeader
            }, 1003)
        },

        /**
         * @records 播放记录
         * @param data 请求参数
         */
        records: function (data) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/user/records",
                data:data,
                beforeSend: cmsHeader
            }, 1004)
        },

        /**
         * @delCollect 删除收藏
         * @param programID 节目id
         */
        delCollect: function (programID) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/removeCollect/"+programID,
                type: "POST",
                beforeSend: cmsHeader
            }, 1005)
        },
        /**
         * @addCollect 添加收藏
         * @param programID 节目id
         */
        addCollect: function(programID) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/addCollect/"+programID,
                type: "POST",
                beforeSend: cmsHeader
            }, 1006)
        },
        /**
         * @collected 查询收藏列表
         * @param data
         */
        collected: function(data) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/user/collects",
                data:data,
                beforeSend: cmsHeader
            }, 1007)
        },

        /**
         * @search 搜索节目
         * @param programID 节目id
         */
        search: function(data) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/search",
                data: data,
                beforeSend: cmsHeader
            }, 1008)
        },
        /**
         * @search 推荐位
         * @param programID 节目id
         */
        promotions: function(option) {
            return ajaxPromise(U.assign({
                url: TOPDRAW_API_SERVER + "api/promotions",
                beforeSend: cmsHeader
            },option || {}), 1009)
        },
        /**
         * @addRecord 添加播放记录
         * @param programID 节目id
         */
        addRecord: function(programID,data) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/addRecord/"+programID,
                type: "POST",
                data: data,
                beforeSend: cmsHeader
            }, 1010)
        },
        /**
         * @episodes 播放内容详情
         * @param episodeid 选集id
         */
        episodes: function(episodeid,data) {
            return ajaxPromise({
                url: TOPDRAW_API_SERVER + "api/episodes/"+episodeid,
                data: data,
                beforeSend: cmsHeader
            }, 1011)
        },

        /**
         * @getBanner 获取推荐位数据
         * @param bannerID
         * @page
         * @pageSize
         */
        getBanner: function(bannerID, page, pageSize){
            var homeRecData = this.promotionsData, my = this;
            if (!!homeRecData) {
                return typeof homeRecData === 'undefined'? null : U.filter(homeRecData,function (item, i) {
                    return  item['name'] == bannerID
                }).slice(page ? (page-1)*pageSize : 0, page && pageSize ? page*pageSize:undefined);
            }else {
                var data = null
                this.promotions({
                    async:false //同步
                }).then(function (resp) {
                    my.promotionsData = resp.data;
                    data = U.filter(resp.data, function (item, i) {
                      console.log(1)
                        return item['name'] === bannerID
                    })
                })
                return data.slice(page ? (page-1)*pageSize : 0, page && pageSize ? page*pageSize:undefined)
            }
            // return typeof homeRecData === 'undefined'? null : U.find(homeRecData,function (i, data) {
            //     return  data['name'] == bannerID
            // })['children'].slice(page ? (page-1)*pageSize : 0, page && pageSize ? page*pageSize:undefined);

        },
        getAuthResult: function(){
            return this.__authRes || null;
        },

        /**
         * @play
         */
        //info 节目详情页数据data； sel 播放索引从0开始； replayTime 设置从几秒开始重复播放
        play: function(info, sel, replayTime, left, top, width, height){
            try {
                window.android.setVid(info, sel)
                window.android.setReplayTime( replayTime )
                this.setPlayMedia(left, top, width, height)
            }catch (e) {

            }
        },
        //销毁播放器
        videoRelease: function(){
            try {
                window.android.videoRelease()
            }catch (e) {

            }
        },
        //
        pause: function(isPlay){
            try {
                window.android.setPlayOrPause(isPlay)
            }catch (e) {

            }
        },
        // 设置播放窗口
        setPlayMedia: function(left, top, width, height){

            if (left && top) {
                window.android.setPosition(left, top);
            }
            if (width && height) {
                window.android.setVideoWidth(width);
                window.android.setVideoHeight(height);
            }
        },

        // ep选集索引 从0开始； seekTime 秒数
        gotoPlayer: function(ep, seekTime){
            try {
                window.android.setWindowFullscreen(ep, seekTime);
            }catch (e) {

            }
        },


        ajaxPromise: ajaxPromise
    }


    window["WkEpg"].Api = MyApi



})(window)


/**
 * @android 监听
 */
var
    //监听窗口播放
    onAndroidMediaPlayerStart = function(){},
    //监听退出全屏播放
    onAndroidMediaPlayerQuit = function(){}
;
