import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'
import Detail from '../views/Detail'
import Colunm from '../views/colunm'
import Search from '../views/search'
import ColAndHis from '../views/colAndHis'
import Video from '../views/video'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index.scss',
    component: Index
  },
  {
    path: '/detail',
    name: 'detail',
    component: Detail
  },
  {
    path: '/colunm',
    name: 'colunm',
    component: Colunm
  },
  {
    path: '/search',
    name: 'search',
    component: Search
  },
  {
    path: '/colAndHis',
    name: 'colAndHis',
    component: ColAndHis
  },
  {
    path: '/video',
    name: 'video',
    component: Video
  }
]
const router = new VueRouter({
  routes
})

export default router
