            <div class="no_select"><img :src="!guessLikeInfo[0]?'':baseURL+guessLikeInfo[0].posters.vertical" alt="" id="area7_pic_0"></div>
            <div class="select"><img src="../assets/images/details/sela_s.png" alt=""></div>
