          </div>
          <div class="item" id="area7_item_2" style="left: 520px;top: 0px;">
            <div class="no_select"><img :src="!guessLikeInfo[2]?'':baseURL+guessLikeInfo[2].posters.vertical" alt="" id="area7_pic_2"></div>
            <div class="select"><img src="../assets/images/details/sela_s.png" alt=""></div>
