          <div class="item" id="area7_item_4" style="left: 0px;top: 300px;">
            <div class="no_select"><img :src="!guessLikeInfo[4]?'':baseURL+guessLikeInfo[4].posters.vertical" alt="" id="area7_pic_4"></div>
            <div class="select"><img src="../assets/images/details/sela_s.png" alt=""></div>
            <p id="area7_txt_4"></p>
          </div>
          <div class="item" id="area7_item_5" style="left: 260px;top: 300px;">
            <div class="no_select"><img :src="!guessLikeInfo[5]?'':baseURL+guessLikeInfo[5].posters.vertical" alt="" id="area7_pic_5"></div>
            <div class="select"><img src="../assets/images/details/sela_s.png" alt=""></div>
            <p id="area7_txt_5"></p>
          </div>
          <div class="item" id="area7_item_6" style="left: 520px;top: 300px;">
            <div class="no_select"><img :src="!guessLikeInfo[6]?'':baseURL+guessLikeInfo[6].posters.vertical" alt="" id="area7_pic_6"></div>
            <div class="select"><img src="../assets/images/details/sela_s.png" alt=""></div>
            <p id="area7_txt_6"></p>
          </div>
          <div class="item" id="area7_item_7" style="left: 780px;top: 300px;">
            <div class="no_select"><img :src="!guessLikeInfo[7]?'':baseURL+guessLikeInfo[7].posters.vertical" alt="" id="area7_pic_7"></div>
            <div class="select"><img src="../assets/images/details/sela_s.png" alt=""></div>
            <p id="area7_txt_7"></p>
          </div>
        </div>

      </div>
