</template>

<script>
import { TOPDRAW_API_SERVER } from '../assets/js/config'
export default {
  data () {
    return {
      proInfo: [],
      episodePage: 1,
      episodeBigPage: 1,
      playURL: '',
      unableToSee: 12,
      pagenum: 0,
      bigunableToSee: 5,
      hasFinallyEpisode: -1,
      bodyPos : ['0', '-620', '-1100'],
      topSearchTitle: ['detailTop3', 'detailTop2', 'detailTop1', 'detailTop4'],
      isCollect: [
        require('../assets/images/details/btn_collect.png'),
        require('../assets/images/details/btn_collect_s.png'),
        require('../assets/images/details/btn_collected.png'),
        require('../assets/images/details/btn_collected_s.png')
      ],
      top_s: require('../assets/images/details/top_s.png'),
      t_gif: require('../assets/images/batch/t.gif'),
      topInfo: [],
      guessLikeInfo: [],
      baseURL: TOPDRAW_API_SERVER
    }
  },
  methods: {
    init: function () {

      var that = this
      var toast = new WkEpg.Eva.Toast()
      var area0, area2, area1, area3, area4, area5, area6, area7

      function FocusMove() {
        this.createArea();
        this.createFunction();
        this.createKeyFunction();
        // this.createPlayerFunction();
        // this.marqueeName();
      }
      FocusMove.prototype = {
        createArea: function () {
          area0 = WkEpg.AreaCreator(1, 1, new Array(-1, 1, 1, 2), "area0_item_", "className:item item_focus", "className:item");
          // area1 = A.render();
          area1 = WkEpg.AreaCreator(1, 1, new Array(0, -1, 3, 2), "area1_item_", "className:item item_focus", "className:item");
          area2 = WkEpg.AreaCreator(1, 1, new Array(0, 1, 3, -1), "area2_item_", "className:item item_focus", "className:item");
          area3 = WkEpg.AreaCreator(1, 12, new Array(1, -1, 4, -1), "area3_item_", "className:item item_focus", "className:item");
          area4 = WkEpg.AreaCreator(1, 5, new Array(3, -1, 5, -1), "area4_item_", "className:item item_focus", "className:item");
          area5 = WkEpg.AreaCreator(1, 4, new Array(4, -1, 6, -1), "area5_item_", "className:item item_focus", "className:item");
          area6 = WkEpg.AreaCreator(1, 4, new Array(5, -1, 7, -1), "area6_item_", "className:item item_focus item_active", "className:item");
          area7 = WkEpg.AreaCreator(2, 4, new Array(6, -1, -1, -1), "area7_item_", "className:item item_focus", "className:item");

          area3.staydirarray = [true, false, true, false];
          area4.staydirarray = [true, false, false, false];
          area5.staydirarray = [true, false, true, false];
          area6.staydirarray = [true, false, true, false];

          // area2.stablemoveindex = [-1, "0-1>0,0-2>0", -1, -1];

          // area3.areaStayInitFocus = false;
          // area4.areaStayInitFocus = false;
          area3.setbroadwiseCrossturnpage(true); //设置横向自动翻页
          area4.setbroadwiseCrossturnpage(true); //设置横向自动翻页
          area4.pagecount = Math.ceil(that.proInfo.episodeCount / 60)
          area3.pagecount = Math.ceil(that.proInfo.episodeCount / 12)
          area3.curpage = that.episodePage
          area4.curpage = Math.ceil(that.episodePage / 5);
          // area4.stayindexarray[0] = ((area3.curpage - 1) % episodeListSize)

          window.pageobj = WkEpg.PageCreator(0, 0, new Array(area0, area1, area2, area3, area4, area5, area6, area7));

          that.guessLikeInfo.length !== 0?that.addTVlink(7,that.guessLikeInfo):''
          that.topInfo.length !== 0?that.addTVlink(5,that.topInfo):''
          window.pageobj.move = WkEpg.Util.afterFn(window.pageobj.move, function (dir) {
            // 屏蔽左右方向
            if (dir == 1 || dir == 3) return;
            switch (this.curareaid) {
              default:
                that.pagenum = 0;
                break;
              case 0:
              case 1:
              case 2:
              case 3:
              case 4:
                that.pagenum = 0;
                break;
              case 5:
              case 6:
                that.pagenum = 1;
                break;
              case 7:
                that.pagenum = 2;
                break;
            }
          })

        },
        createFunction: function () {
          area3.areaPageTurnEvent = function (num) {
            that.episodePage+=num

            if (this.curpage == this.pagecount) {
              var min = 12*(that.episodePage-1)+1
              this.datanum = that.proInfo.episodeCount - min +1
              that.unableToSee = 12 - this.datanum
            } else {
              this.datanum = 12
              that.unableToSee = 0
            }
            if (num === 1) {
              if (window.pageobj.areas[4].stayindexarray[0] == 4) {
                window.pageobj.areas[4].curpage+=1
                window.pageobj.areas[4].stayindexarray[0] = 0
                window.pageobj.areas[4].areaPageTurnEvent(1)

              } else {
                window.pageobj.areas[4].stayindexarray[0] += 1
                // window.pageobj.areas[4].curindex=parseInt(window.pageobj.areas[4].curindex)+1
                window.pageobj.areas[4].areaPageTurnEvent(0)
              }
            } else if (num === -1) {
              if (window.pageobj.areas[4].stayindexarray[0] == 0) {
                window.pageobj.areas[4].curpage-=1
                window.pageobj.areas[4].stayindexarray[0] = 4
                window.pageobj.areas[4].areaPageTurnEvent(-1)
              } else {
                window.pageobj.areas[4].stayindexarray[0]-=1
                window.pageobj.areas[4].areaPageTurnEvent(0)
              }
            }
            // if (this.pagecount <= 1) {
            //   return;
            // }
            // this.lock = true;
            // controlObj.renderEpisodeList()
          }
          area4.areaPageTurnEvent = function (num) {
            that.episodeBigPage+=num
            if (this.curpage == this.pagecount) {
              var min = (that.episodeBigPage-1)*60+12
              this.datanum = Math.ceil((that.proInfo.episodeCount - min)/12)+1
              that.bigunableToSee = 5 - this.datanum
              that.hasFinallyEpisode = this.datanum-1
            } else {
              this.datanum = 5
              that.hasFinallyEpisode = false
              that.bigunableToSee = -1
            }
            // if (this.pagecount <= 1) {
            //   return;
            // }
            // this.lock = true;
            // controlObj.renderEpisodeList()
          }
          window.pageobj.areas[3].areaPageTurnEvent(0)
          window.pageobj.areas[4].areaPageTurnEvent(0)
          // area3.changefocusAfterEvent = function () {
          //   console.log('换')
          //   // var curindex = this.curindex + (this.curpage - 1) * episodeListSize + 1;
          //   // area3.pageturn(curindex - area3.curpage)
          // }

          area4.changefocusAfterEvent = function () {
            window.pageobj.areas[3].curpage = that.episodePage = (this.curpage-1)*5+this.curindex+1
            window.pageobj.areas[3].areaPageTurnEvent(0)
            // var curindex = this.curindex + (this.curpage - 1) * episodeListSize + 1;
            // area3.pageturn(curindex - area3.curpage)
          }

          area6.changefocusAfterEvent = function () {
            that.getTops(that.topSearchTitle[this.curindex])
          }

          var bindTvlink = function () {
            if (!this.doms[this.curindex].tvlink) return;
            var link = this.doms[this.curindex].tvlink
            var msg = typeof link == "string"?link.substring(link.indexOf('=')+1):link.toString()
            var assetId = {
              assetId: msg
            }
            that.setReturnUrl(assetId)
            // window.location.href = this.doms[this.curindex].tvlink + and + "returnurl=" + encodeURIComponent(getReturnUrl(this.id, this.curindex));
          }
          area0.areaOkEvent = function () {
            that.playURL = that.proInfo.episodes[0].play_url
            var playURL = {
              playURL: that.playURL
            }
            window.pageobj = null
            that.$store.commit('setReturnMsg', playURL)
            that.$router.push('/video')
          }
          area1.areaOkEvent = async function () {
            // 更改收藏状态
            var result = {}
            if (that.proInfo.isCollected) {
              result = await that.$http.post('/removeCollect/'+that.proInfo.id)
            } else {
              result = await that.$http.post('/addCollect/'+that.proInfo.id)
            }
            if (result.data.status == true) {
              that.proInfo.isCollected = !that.proInfo.isCollected
            }
          }
          area3.areaOkEvent = function () {
            var playNum = $("#area3_item_" + this.curindex).html();
            that.playURL = that.proInfo.episodes[playNum-1+''].play_url
            var playURL = {
              playURL: that.playURL
            }
            window.pageobj = null
            that.$store.commit('setReturnMsg', playURL)
            that.$router.push('/video')
            // if (dataObj.isSub == "1") {
            //   if (!dataObj.episodes[playNum - 1]['play_url']) {
            //     notice.echo("暂无内容，正在努力上内容中", null, "提示");
            //     return
            //   }
            //   dataObj.play(playNum - 1, dataObj.recordEpisodeNum == playNum ? dataObj.seektime : 0)
            //   window.location.href='video.html?assetid='+programCode+'&episodes='+playNum+'&backUrl='+window.location.href;
            //
            // } else {
            //   notice.error("鉴权不通过，暂时无法播放jishu")
            //   window.location.href='./buy.html?'+'backUrl='+window.location.href
            // }

          }
          area5.areaOkEvent = bindTvlink
          area7.areaOkEvent = bindTvlink

          area7.areaInBeforeEvent = function () {
            toast.echo("按「返回键」返回顶部", "提示")
          }
        },
        createKeyFunction: function () {
          WkEpg.Nav.keyBackEvent = function () {
            if (that.pagenum !== 0) {
              that.pagenum = 0;
              pageobj.changefocus(0, 0)//焦点归位
            } else {
              window.pageobj = null
              console.log(that.$store.state.returnUrl)
              that.$router.go(-1)
            }
          };
        }
      }
      this.focusObj = new FocusMove();
    },
    getInfoById: async function () {
      const { data: res } = await this.$http.get(`/programs/${this.$store.state.assetId}`)
      this.proInfo = res.data
    },
    getTops: function (info) {
      this.topInfo = this.$store.getters.getProByName(info)
      window.pageobj != null?this.addTVlink(5,this.topInfo):''
    },
    getGuessLike: async function () {
      const { data: res } = await this.$http.get('/categories/1/programs', {
        params: {
          page: 1,
          pageSize: 8
        }
      })
      this.guessLikeInfo = res.data
      $('.area_connection .item').css("display", 'block')
      window.pageobj != null?this.addTVlink(7,this.guessLikeInfo):''
    },
    addTVlink: function (areaId,infoName,num) {
      if (infoName.length == 0) return
      var leng = !window.pageobj.areas[areaId].datanum?window.pageobj.areas[areaId].doms.length:window.pageobj.areas[areaId].datanum
      var step = num?parseInt(num):0
      for (var i = 0;i < leng;i++) {
        window.pageobj.areas[areaId].doms[i].tvlink = !infoName[i+step].url?infoName[i+step].id:infoName[i+step].url
      }
    },
    setReturnUrl: function(anyMsg) {
      var param = {
      }
      if (anyMsg !== undefined) {
        for (var key in anyMsg) {
          param[key] = anyMsg[key]
        }
      }
      this.$store.commit('setReturnMsg', param)
      window.pageobj.changefocus(0,0)
      window.pageobj = null
      this.episodePage= 1
        this.episodeBigPage= 1
        this.unableToSee= 12
        this.bigunableToSee= 5
        this.hasFinallyEpisode= -1
      this.getInfoById()
      this.pagenum = 0
    }
  },
  mounted() {
    // this.init()
  },
  created() {
    this.getInfoById()
    this.getTops('detailTop3')
    this.getGuessLike()
  },
  watch: {
    proInfo: function (val) {
      if (val.length == 0) return
      // 当有数据之后将一些该消失的消失该显示的显示
      $('.cloth').css("display", 'none')
      $('.area_episodeList').css('visibility','visible')
      $('.area_episodes').css('visibility','visible')
      this.$nextTick(function () {
        this.init()
      })
    },
    pagenum: function (val) {
      $('.container').css("top", this.bodyPos[this.pagenum] + "px")
    }
  }
}
</script>

