import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    promotions: [],
    returnUrl: '',
    areaId: '',
    indexId: '',
    magicCount: 1,
    IndexPageNum: '',
    assetId: '',
    starpage: '',
    colunm_index:'',
    magicInfo: [],
    famousInfo: [],
    exitInfo: [],
    playURL: '',
    searchVal: '',
    colOrHis: '',
    colOrHisIndex: ''
  },
  mutations: {
    changePro: function (state, list) {
      state.promotions = list
    },
    setReturnMsg: function (state, itemObj) {
      if (itemObj.areaId == 19) {
        itemObj.areaId = 1
        itemObj.indexId = 0
      }
      for (var key in itemObj) {
        state[key] = itemObj[key]
      }
    },
    addMagicMsg: function (state, data) {
      state.magicInfo = data
    },
    clickMagicCount: function (state) {
      state.magicCount == 3?state.magicCount=1:state.magicCount++
    },
    addFamousMsg: function (state, data) {
      state.famousInfo = data
    },
    addExitMsg: function (state, data) {
      state.exitInfo = data
    }
  },
  actions: {
    getPromotions: async function (context) {
      const { data: res } = await axios.get('/promotions')
      context.commit('changePro', res.data)
    },
    getMagic: async function (context) {
      const { data: res } = await axios.get(`categories/${context.state.magicCount}/programs`, {
        params: {
          page:5,
          pageSize:8
        }
      })
      context.commit('addMagicMsg', res.data)
    },
    getFamousInfo: async function (context, page) {
      const { data: res } = await axios.get(`categories/2/programs`, {
        params: {
          page:page !== undefined?page:1,
          pageSize:page !== undefined?4:5,
        }
      })
      if (page !== undefined) {
        context.commit('addExitMsg', res.data)
      }else {
        context.commit('addFamousMsg', res.data)
      }
    }
  },
  getters: {
    getProByName: function (state) {
      return function (name) {
        return state.promotions.filter(x => x.name === name)
      }
    }
  },
  modules: {
  }
})
