<script>
  import { TOPDRAW_API_SERVER } from '../assets/js/config'
  export default {
    data() {
      return {
        title: [
          require("../assets/images/batch/menu1/anime.png"),
          require("../assets/images/batch/menu1/newfilm.png"),
          require("../assets/images/batch/menu1/top.png"),
        ],
        defaultImg: require('../assets/images/t.gif'),
        activeFlag: -1,
        focusobj: null,
        curpage: 1,
        viewInfo: [],
        viewTotal: -1,
        baseURL : TOPDRAW_API_SERVER
      }
    },
    created() {
      this.getInfo()
    },
    mounted () {
      this.init()
    },
    methods: {
      getInfo: function () {
        WkEpg.pageobj = null
        switch (this.$store.state.colunm_index) {
          case "top":
            this.activeFlag = 2
            break;
          case "dongm":
            this.activeFlag = 0
            break;
          case "newfilm":
            this.activeFlag = 1
            break;
        }
        console.log(this.activeFlag)
      },
      init: function () {
        var that = this
        var area0,area2
        //焦点移动类
        function FocusMove() {
          this.createArea();
          this.createFunction();
        }
        FocusMove.prototype = {
          createArea: function () {
            area0 = WkEpg.AreaCreator(3, 1, [-1, -1, -1, 1], "area0_item_", "className:item item_focus", "className:item");
            area2 = WkEpg.AreaCreator(2, 5, [-1, 0, -1, -1], "area2_item_", "className:item item_focus", "className:item");

            area0.staydirarray = [false, false, false, true];
            area0.stayindexarray = [0, 0, 0, that.activeFlag];
            area2.setendwiseCrossturnpage(true);
            area2.areaStayInitFocus = true;
            area2.curpage = that.curpage
            if (that.viewTotal !== -1) area2.pagecount = that.viewTotal

            window.pageobj = WkEpg.PageCreator(1, 0, [area0, area2]);
            console.log(window.pageobj)

          },
          createFunction: function () {

            WkEpg.Nav.keyBackEvent = function() {
              // var param = {
              //   colOrHisPage: '',
              //   colOrHisIndex: ''
              // }
              // that.$store.commit('setReturnMsg', param)
              that.$router.go(-1)
            };

            area2.areaPageTurnEvent = function (num) {
              switch (num) {
                case -1:
                  if (that.curpage === 1) return
                    break;
                case 1:
                  if (that.curpage === that.viewTotal) return;
                    break;
              }
              that.curpage+=num
              // if (this.pagecount <= 1) return;
              // dataObj.handleList().then(function () {
              //   if (area2.curpage == area2.pagecount) {
              //     pageobj.changefocus(2, 0);
              //   } else {
              //     area2.pageTurnAndChangeFocus();
              //   }
              // });

            };

            area2.changefocusAfterEvent = function () {
              that.renderFlag = true
              if (this.doms[this.curindex] != undefined) {
                $("#area2_select_txt_" + this.curindex).html("<marquee direction='left' scrolldelay='100'>"+(!that.viewInfo[this.curindex]?'':that.viewInfo[this.curindex].name)+"</marquee>");
              }
            }

            // 区域内切换焦点触发事件
            area0.changefocusAfterEvent = async function() {
              that.activeFlag = this.curindex
              switch (this.curindex) {
                case 0:

                  break;
                case 1:

                  break;
              }
            };

          }
        }

        if (!this.focusObj) this.focusObj = new FocusMove();
      },
      subName: function (val) {
        if (val.length>7) {
          return val.substring(0,6) + '...'
        } else{
          return val
        }
      }
    },
    watch: {
      activeFlag: async function (val) {
        this.renderFlag = false
        this.curpage = 1
        const { data: res } = await this.$http.get(`/categories/${val+1}/programs`, {
          params: {
            page: this.curpage,
            pageSize: 10
          }
        })
        this.viewInfo = res.data
        this.viewTotal = res.meta.pagination.total_pages
        if (window.pageobj !== null) {
          window.pageobj.areas[1].datanum = this.viewInfo.length
          window.pageobj.areas[1].pagecount = this.viewTotal
          window.pageobj.areas[1].curpage = this.curpage
          window.pageobj.areas[1].changefocusAfterEvent()
        }
        console.log(this.viewInfo)
      },
      curpage: async function (val) {
        if (!this.renderFlag) return
        const { data: res } = await this.$http.get(`/categories/${this.activeFlag+1}/programs`, {
          params: {
            page: val,
            pageSize: 10
          }
        })
        this.viewInfo = res.data
        if (val === this.viewTotal) {
          window.pageobj.areas[1].datanum = this.viewInfo.length
          if (window.pageobj.areas[1].curindex>=this.viewInfo.length) window.pageobj.changefocus(1,this.viewInfo.length-1)
        } else {
          window.pageobj.areas[1].datanum = 10
        }
        window.pageobj.areas[1].changefocusAfterEvent()
      }
    }
  }
</script>

